import React, { useState, useContext } from "react";
import {
  PrimaryButton,
  Stack,
  DefaultButton,
  ProgressIndicator,
  StackItem,
} from "@fluentui/react";
import CreateDocument from "../dialogs/CreateDocument.js";
import DesignsContext from "../../context/designs/designsContext";

function LeftBar() {
  //============================================================================
  const designsContext = useContext(DesignsContext);
  const { getFiles } = designsContext;
  //============================================================================
  //---------------------------------------------------------------------------
  const [leftbar, setLeftbar] = useState({
    createDocumentDLG: false,
    folder: 0,
  });
  const { folder, createDocumentDLG } = leftbar;
  //---------------------------------------------------------------------------
  const onClickBTN = async (e) => {
    //===================================================

    //===================================================
    getFiles(e);
    setLeftbar({ ...leftbar, folder: e });
  };
  const onCreateDocumentDLG = () => {
    setLeftbar({ ...leftbar, createDocumentDLG: !createDocumentDLG });
  };
  //---------------------------------------------------------------------------
  return (
    <Stack
      styles={{
        root: {
          height: "100%",
          width: "180px",
          paddingLeft: "5px",
          paddingRight: "5px",
          paddingTop: "5px",
          borderRightStyle: "solid",
          borderRightWidth: "1px",
          borderRightColor: "#D5DBDB",
        },
      }}
    >
      <CreateDocument
        showDLG={createDocumentDLG}
        closeDLG={onCreateDocumentDLG}
      />
      <Stack tokens={{ childrenGap: "5px" }}>
        <PrimaryButton
          text="Create Document"
          iconProps={{ iconName: "CirclePlus" }}
          styles={{
            root: {
              paddingLeft: "5px",
            },
          }}
          onClick={onCreateDocumentDLG}
        />

        <div style={{ borderTop: "1px solid #bfbfbf", marginTop: "5px" }} />
        <DefaultButton
          text="My documents"
          iconProps={{ iconName: "DocumentSet" }}
          checked={folder === 0 ? true : false}
          onClick={() => onClickBTN(0)}
          styles={{
            root: {
              paddingLeft: "10px",
            },
          }}
          style={{
            backgroundColor: folder === 0 ? "#E1DFDD" : null,
          }}
        />
        {/*<DefaultButton
          text="Recently opened"
          checked={folder === 1 ? true : false}
          iconProps={{ iconName: "FabricOpenFolderHorizontal" }}
          onClick={() => onClickBTN(1)}
          styles={{
            root: {
              paddingLeft: "10px",
            },
          }}
        />*/}
        <DefaultButton
          text="Shared with me"
          checked={folder === 1 ? true : false}
          iconProps={{ iconName: "PeopleRepeat" }}
          onClick={() => onClickBTN(1)}
          styles={{
            root: {
              paddingLeft: "10px",
            },
          }}
          style={{
            backgroundColor: folder === 1 ? "#E1DFDD" : null,
          }}
        />
        <DefaultButton
          text="Public examples"
          checked={folder === 2 ? true : false}
          iconProps={{ iconName: "GlobeFavorite" }}
          onClick={() => onClickBTN(2)}
          styles={{
            root: {
              paddingLeft: "10px",
            },
          }}
          style={{
            backgroundColor: folder === 2 ? "#E1DFDD" : null,
          }}
        />

        <div style={{ borderTop: "1px solid #bfbfbf", marginTop: "5px" }} />
        <DefaultButton
          text="Trash"
          checked={folder === 3 ? true : false}
          iconProps={{ iconName: "RecycleBin" }}
          onClick={() => onClickBTN(3)}
          styles={{
            root: {
              paddingLeft: "10px",
            },
          }}
          style={{
            backgroundColor: folder === 3 ? "#E1DFDD" : null,
          }}
        />
        <div style={{ borderTop: "1px solid #bfbfbf", marginTop: "50px" }} />
        <ProgressIndicator
          label="Storage (93% full)"
          description="14 GB of 15 GB used"
          percentComplete={0.9}
          barHeight={4}
          styles={{
            progressTrack: {
              backgroundColor: "#D5DBDB",
            },
          }}
        />
        <ProgressIndicator
          label="Credit (50% used)"
          percentComplete={0.5}
          barHeight={4}
          styles={{
            progressTrack: {
              backgroundColor: "#D5DBDB",
            },
          }}
        />
      </Stack>
      <StackItem grow>
        <div></div>
      </StackItem>
      <StackItem>
        <div
          style={{
            borderTop: "1px solid #bfbfbf",
            marginTop: "25px",
            marginBottom: "25px",
          }}
        />
        <Stack.Item>
          <p style={{ textAlign: "center" }}>
            You are using the free version of EngiPipe.
          </p>
          <DefaultButton
            text="Upgrade"
            style={{
              backgroundColor: "#0f9960",
              color: "#fff",
              marginBottom: 10,
            }}
          />
        </Stack.Item>
      </StackItem>
    </Stack>
  );
}

export default LeftBar;
