import React, { useContext, useEffect, useState, Fragment } from "react";
import PropTypes from "prop-types";
import DocContext from "../../context/doc/docContext";
import DimInput from "../input/DimInput";

import {
  Dialog,
  Stack,
  DefaultButton,
  PrimaryButton,
  DialogFooter,
  DialogType,
  MessageBar,
  MessageBarType,
} from "@fluentui/react";

const DefaultUnits = ({ showDLG, closeDLG }) => {
  //============================================================================
  const [iUnits, setIUnits] = useState();
  const [changed, setChanged] = useState([]);
  const [showWarning, setShowWarning] = useState(false);
  //============================================================================
  const docContext = useContext(DocContext);
  const { units, setUnits, setDefaultUnits } = docContext;
  //============================================================================
  const modelProps = {
    isBlocking: false,
  };
  //============================================================================
  useEffect(() => {
    if (units) setIUnits(units);
    // eslint-disable-next-line
  }, [units]);
  //--
  useEffect(() => {
    if (document && !units) {
      setDefaultUnits("SI");
    }
    // eslint-disable-next-line
  }, [document]);
  //============================================================================
  const onCancel = () => {
    if (changed.length !== 0 && !showWarning) {
      setShowWarning(true);
    } else {
      closeDLG();
      setChanged([]);
      setIUnits(units);
    }
  };
  //============================================================================
  const onApply = () => {
    if (changed) {
      //== call change units from docContext
      setUnits(iUnits, changed);
      setChanged([]);
      closeDLG();
    } else {
      closeDLG();
    }
  };
  //============================================================================
  const onChangeUnit = (v, u, t) => {
    if (!iUnits) return;

    let a = changed;
    const c = a.indexOf(t);
    if (c === -1 && units[t] !== u) {
      a.push(t);
    } else if (c !== -1 && units[t] === u) {
      a = a.filter(function (value, index, arr) {
        return index !== c;
      });
    }

    setChanged(a);
    setShowWarning(false);

    setIUnits({
      ...iUnits,
      [t]: u,
    });
  };
  //============================================================================
  const warning = (
    <Fragment>
      Default Units
      {showWarning ? (
        <MessageBar
          messageBarType={MessageBarType.warning}
          isMultiline={false}
          dismissButtonAriaLabel="Close"
        >
          You have changed some of the fields. To omit your changes please press
          cancel again.
        </MessageBar>
      ) : null}
    </Fragment>
  );
  //============================================================================
  const dialogContentProps = {
    type: DialogType.largeHeader,
    title: warning,
    subText:
      "By setting default units, if you have not selected a unit for a quantity yet, the default unit will be selected automatically.",
  };
  //============================================================================
  return (
    <Dialog
      hidden={!showDLG}
      onDismiss={onCancel}
      dialogContentProps={dialogContentProps}
      modalProps={modelProps}
      minWidth={700}
    >
      <Stack horizontal>
        <Stack tokens={{ childrenGap: 5 }}>
          <DimInput
            unit={iUnits ? iUnits.smallLength : null}
            unitFamily="smallLength"
            title="Length (small)"
            onChange={(v, u) => onChangeUnit(v, u, "smallLength")}
          />
          <DimInput
            unit={iUnits ? iUnits.largeLength : null}
            unitFamily="largeLength"
            title="Length (large)"
            onChange={(v, u) => onChangeUnit(v, u, "largeLength")}
          />
          <DimInput
            unit={iUnits ? iUnits.Velocity : null}
            unitFamily="Velocity"
            title="Velocity"
            onChange={(v, u) => onChangeUnit(v, u, "Velocity")}
          />
          <DimInput
            unit={iUnits ? iUnits.Pressure : null}
            unitFamily="Pressure"
            title="Pressure"
            onChange={(v, u) => onChangeUnit(v, u, "Pressure")}
          />
          <DimInput
            unit={iUnits ? iUnits.Angle : null}
            unitFamily="Angle"
            title="Angle"
            onChange={(v, u) => onChangeUnit(v, u, "Angle")}
          />
          <DimInput
            unit={iUnits ? iUnits.Density : null}
            unitFamily="Density"
            title="Density"
            onChange={(v, u) => onChangeUnit(v, u, "Density")}
          />
          <DimInput
            unit={iUnits ? iUnits.Mass : null}
            unitFamily="Mass"
            title="Mass"
            onChange={(v, u) => onChangeUnit(v, u, "Mass")}
          />
          <DimInput
            unit={iUnits ? iUnits.Time : null}
            unitFamily="Time"
            title="Time"
            onChange={(v, u) => onChangeUnit(v, u, "Time")}
          />
          <DimInput
            unit={iUnits ? iUnits.Viscosity : null}
            unitFamily="Viscosity"
            title="Viscosity"
            onChange={(v, u) => onChangeUnit(v, u, "Viscosity")}
          />
          <DimInput
            unit={iUnits ? iUnits.nonNewtonianViscosity : null}
            unitFamily="nonNewtonianViscosity"
            title="nonNewtonian Viscosity"
            onChange={(v, u) => onChangeUnit(v, u, "nonNewtonianViscosity")}
          />
        </Stack>
        <Stack tokens={{ childrenGap: 5 }}>
          <DimInput
            unit={iUnits ? iUnits.Volume : null}
            unitFamily="Volume"
            title="Volume"
            onChange={(v, u) => onChangeUnit(v, u, "Volume")}
          />
          <DimInput
            unit={iUnits ? iUnits.Temperature : null}
            unitFamily="Temperature"
            title="Temperature"
            onChange={(v, u) => onChangeUnit(v, u, "Temperature")}
          />

          <DimInput
            unit={iUnits ? iUnits.pressureGradient : null}
            unitFamily="pressureGradient"
            title="Pressure Gradient"
            onChange={(v, u) => onChangeUnit(v, u, "pressureGradient")}
          />
          <DimInput
            unit={iUnits ? iUnits.Power : null}
            unitFamily="Power"
            title="Power"
            onChange={(v, u) => onChangeUnit(v, u, "Power")}
          />
          <DimInput
            unit={iUnits ? iUnits.Frequency : null}
            unitFamily="Frequency"
            title="Frequency"
            onChange={(v, u) => onChangeUnit(v, u, "Frequency")}
          />
          <DimInput
            unit={iUnits ? iUnits.Compressibility : null}
            unitFamily="Compressibility"
            title="Compressibility"
            onChange={(v, u) => onChangeUnit(v, u, "Compressibility")}
          />
          <DimInput
            unit={iUnits ? iUnits.Energy : null}
            unitFamily="Energy"
            title="Energy"
            onChange={(v, u) => onChangeUnit(v, u, "Energy")}
          />
          <DimInput
            unit={iUnits ? units.massFlowRate : null}
            unitFamily="massFlowRate"
            title="Mass FlowRate"
            onChange={(v, u) => onChangeUnit(v, u, "massFlowRate")}
          />
          <DimInput
            unit={iUnits ? units.molarMass : null}
            unitFamily="molarMass"
            title="Molar Mass"
            onChange={(v, u) => onChangeUnit(v, u, "molarMass")}
          />
          <DimInput
            unit={iUnits ? units.surfaceTension : null}
            unitFamily="surfaceTension"
            title="Surface Tension"
            onChange={(v, u) => onChangeUnit(v, u, "surfaceTension")}
          />
        </Stack>
      </Stack>

      <DialogFooter>
        <PrimaryButton onClick={onApply} text="Apply changes" />
        <DefaultButton onClick={onCancel} text="Cancel" />
      </DialogFooter>
    </Dialog>
  );
};

DefaultUnits.propTypes = {
  showDLG: PropTypes.bool.isRequired,
  closeDLG: PropTypes.func.isRequired,
};

export default DefaultUnits;
