import React, { useEffect, useContext, useState, Fragment } from "react";
//import { AccountContext } from "../auth/Account";
import AuthContext from "../../context/auth/authContext";
import DesignsContext from "../../context/designs/designsContext";

import NavBar from "../workspace/NavBar";
import Graph from "./Graph";

//import DocContext from "../../context/doc/docContext";

import { Stack, IconButton } from "@fluentui/react";

const Visualize = (props) => {
  //============================================================================
  //const { logout, getSession } = useContext(AccountContext);
  const authContext = useContext(AuthContext);
  const { getSession, authenticate } = authContext;
  //============================================================================
  const [user, setUser] = useState({
    accessToken: null,
    idToken: null,
  });
  //============================================================================
  const [plots, setPlots] = useState([]);
  //============================================================================
  const designsContext = useContext(DesignsContext);
  const { getFiles } = designsContext;
  //============================================================================
  //const docContext = useContext(DocContext);
  //============================================================================

  useEffect(() => {
    getSession()
      .then((session) => {
        setUser({ accessToken: session.accessToken, idToken: session.idToken });
        //clearDoc();
        //clearVisualizations();
        //getFiles(0);
      })
      .catch((err) => {
        props.history.push("/login");
      });
    // eslint-disable-next-line
  }, []);
  //---------------------------------------------------------------------------
  //tokens={{ root: { position: "fixed" } }} verticalFill
  return (
    <Fragment>
      <div
        style={{
          position: "absolute",
          bottom: 65,
          right: 50,
          width: 50,
          height: 50,
          zIndex: 99999,
        }}
      >
        <button
          style={{
            backgroundColor: "#2A5FAA",
            border: "none",
            color: "white",
            padding: "2px 10px",
            textAlign: "center",
            textDecoration: "none",
            display: "inline-block",
            fontSize: 16,
            margin: "2px 8px",
            borderRadius: "50%",
            fontSize: 30,
            cursor: "pointer",
          }}
          onClick={() => setPlots([...plots, { key: plots.length }])}
        >
          +
        </button>
      </div>
      <Stack verticalFill styles={{ root: { width: "100%" } }}>
        <Stack.Item order={1} disableShrink>
          <NavBar />
        </Stack.Item>
        <Stack.Item order={2} grow verticalFill>
          <Stack
            styles={{
              root: {
                height: window.innerHeight - 36,
                overflowY: "scroll",
              },
            }}
            wrap
            horizontal
          >
            {plots.map((plot) => {
              return <Graph width={500} key={plot.key} />;
            })}
          </Stack>
        </Stack.Item>
      </Stack>
    </Fragment>
  );
};

export default Visualize;
