/* eslint-disable */
import React, { useState, useEffect } from "react";
import UserPool from "./UserPool";
import { Link } from "react-router-dom";
//import AuthContext from "../../context/auth/authContext";

import {
  PrimaryButton,
  Stack,
  TextField,
  FontSizes,
  FontWeights,
  MessageBar,
  MessageBarType,
  Spinner,
  SpinnerSize,
  Dialog,
  DialogType,
  DialogFooter,
  DefaultButton,
} from "@fluentui/react";

import logo from "../../assets/img/logo.png";

const Signup = (props) => {
  //============================================================================
  //const authContext = useContext(AuthContext);
  //const { loadUser, isAuthenticated } = authContext;
  //============================================================================
  const [user, setUser] = useState({
    name: "",
    email: "",
    password: "",
    confirmpassword: "",
    company: "",
    isLoading: false,
    errorMessage: null,
    hideDialog: false,
  });
  const {
    name,
    email,
    password,
    confirmpassword,
    company,
    isLoading,
    errorMessage,
    hideDialog,
  } = user;
  //============================================================================
  useEffect(() => {
    //if (!isAuthenticated) loadUser();
  });
  //============================================================================
  /*useEffect(() => {
    if (isAuthenticated) {
      setUser({ isWaiting: false, errorMessage: null });
      props.history.push("/");
    }
    // eslint-disable-next-line
  }, [isAuthenticated, props.history]);*/
  //============================================================================
  const validateEmail = (email) => {
    // eslint-disable-next-line
    var re = /^(([^<>()\[\]\\.,;:\s@"]+(\.[^<>()\[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/;
    return re.test(String(email).toLowerCase());
  };
  //============================================================================
  function checkPassword(str) {
    let a = /[A-Z]/.test(str);
    a = a && /[a-z]/.test(str);
    a = a && /[0-9]/.test(str);
    a = a && /[!@#$%^&*()_+\-=\[\]{};':"\\|,.<>\/?]+/.test(str);
    return a;
  }
  //============================================================================
  const onChange = (e) => {
    setUser({
      ...user,
      [e.target.name]: e.target.value,
      errorMessage: null,
    });
  };
  //============================================================================
  const onSubmit = (e) => {
    setUser({
      ...user,
      isLoading: true,
    });
    e.preventDefault();
    if (
      email === "" ||
      password === "" ||
      confirmpassword === "" ||
      company === "" ||
      name === ""
    ) {
      setUser({
        ...user,
        errorMessage: "Please set all fields.",
        isLoading: false,
      });
    } else if (!validateEmail(email)) {
      setUser({
        ...user,
        errorMessage: "Please provide a valid email address.",
        isLoading: false,
      });
    } else if (password.length < 8) {
      setUser({
        ...user,
        errorMessage: "Your password must be at least 8 characters long.",
        isLoading: false,
      });
    } else if (!checkPassword(password)) {
      setUser({
        ...user,
        errorMessage:
          "Your password must have small and captial letters, numbers and symbols.",
        isLoading: false,
      });
    } else if (password !== confirmpassword) {
      setUser({
        ...user,
        errorMessage: "Passwords do not match.",
        isLoading: false,
      });
    } else {
      //== we can go ahead and ask server to save the user
      UserPool.signUp(
        email,
        password,
        [
          {
            Name: "name",
            Value: name,
          },
          {
            Name: "family_name",
            Value: "---",
          },
          {
            Name: "custom:company",
            Value: company,
          },
        ],
        null,
        (err, data) => {
          if (err) {
            if (err.code === "UsernameExistsException") {
              setUser({
                ...user,
                errorMessage: "This email is already registered.",
                isLoading: false,
              });
            } else {
              setUser({
                ...user,
                errorMessage: "Some error has happend.",
                isLoading: false,
              });
            }
          } else {
            console.log(data);
            setUser({
              ...user,
              hideDialog: true,
              isLoading: false,
            });
          }
        }
      );
    }
  };
  //============================================================================
  const errMsg = (
    <MessageBar
      messageBarType={MessageBarType.severeWarning}
      dismissButtonAriaLabel="Close"
      onDismiss={() => setUser({ ...user, errorMessage: null })}
    >
      {errorMessage}
    </MessageBar>
  );
  //============================================================================

  const toggleHideDialog = () => {
    window.location.href = "/";
  };

  const dialogContentProps = {
    type: DialogType.normal,
    title: "Successful Registration",
    closeButtonAriaLabel: "Close",
    subText:
      "You have been registered successfuly. You will receive an email with confirmation link. After confirmiation you can login to the system.",
  };
  const successDialog = (
    <Dialog
      hidden={!hideDialog}
      onDismiss={toggleHideDialog}
      closeButtonAriaLabel="Close"
      dialogContentProps={dialogContentProps}
    >
      <DialogFooter>
        <PrimaryButton onClick={toggleHideDialog} text="Ok" />
      </DialogFooter>
    </Dialog>
  );
  //============================================================================
  return (
    <Stack
      horizontalAlign="center"
      style={{ margin: "auto" }}
      grow={true}
      tokens={{ maxWidth: 360 }}
    >
      <Stack.Item>
        <div style={{ height: "10px" }} />
      </Stack.Item>
      <Stack.Item>
        <Link to="/">
          <img src={logo} alt="Engipipe Logo" />
        </Link>
      </Stack.Item>
      <Stack.Item>
        <form onSubmit={onSubmit}>
          <Stack
            style={{
              border: "1px solid #c0c0c0",
              borderRadius: "5px",
              padding: "20px",
              marginTop: "10px",
              width: "320px",
            }}
            tokens={{ childrenGap: 10 }}
          >
            {(errorMessage === null) | (errorMessage === "") ? null : errMsg}
            {successDialog}
            <span
              style={{
                fontSize: FontSizes.large,
                fontWeight: FontWeights.bold,
                textAlign: "center",
              }}
            >
              Sign Up
            </span>
            <span>(All fields are required)</span>
            <Stack.Item>
              <TextField
                placeholder="Email"
                name="email"
                onChange={onChange}
                value={email}
                autoComplete="off"
              />
            </Stack.Item>
            <Stack.Item>
              <TextField
                placeholder="Password"
                type="password"
                name="password"
                onChange={onChange}
                value={password}
                autoComplete="off"
              />
            </Stack.Item>
            <Stack.Item>
              <TextField
                placeholder="Confirm Password"
                type="password"
                name="confirmpassword"
                onChange={onChange}
                value={confirmpassword}
                autoComplete="off"
              />
            </Stack.Item>
            <Stack.Item>
              <TextField
                placeholder="Complete Name"
                name="name"
                onChange={onChange}
                value={name}
                autoComplete="off"
              />
            </Stack.Item>
            <Stack.Item>
              <TextField
                placeholder="Company"
                name="company"
                onChange={onChange}
                value={company}
                autoComplete="off"
              />
            </Stack.Item>
            <Stack
              horizontal={true}
              grow
              tokens={{ childrenGap: 5, padding: 10 }}
              horizontalAlign="space-between"
            >
              <Stack.Item align="center">
                <PrimaryButton
                  text="Sign up"
                  type="submit"
                  allowDisabledFocus
                  checked={true}
                  style={{ width: "110px" }}
                  disabled={isLoading}
                >
                  {isLoading ? <Spinner size={SpinnerSize.small} /> : null}
                </PrimaryButton>
              </Stack.Item>
              <Stack.Item align="center">
                <Link to="/login">Already have account?</Link>
              </Stack.Item>
            </Stack>
          </Stack>
        </form>
      </Stack.Item>
    </Stack>
  );
};

export default Signup;
