/* eslint-disable */
import {
  SET_DOCUNITS,
  SET_CURRENTFILE,
  SET_DOCLOADING,
  GET_DOC,
  SET_FITTODATA,
  SET_ZOOM,
  ADD_NODE,
  ADD_EDGE,
  ADD_ELEMENTS,
  SET_TOOLBARSTATE,
  SET_CONTEXTMENU,
  SET_CURRENTEDGE,
  SET_CURRENTNODE,
  SET_CURRENTELEMENT,
  SET_ERROR,
  CLEAR_DOC,
  LOAD_DOC,
  SET_HELP,
} from "../types";

export default (state, action) => {
  switch (action.type) {
    case LOAD_DOC:
      return {
        ...state,
        document: action.payload.doc,
        lastSave: new Date().getTime(),
        hasChanged: action.payload.changeStructure,
        loading: false,
        id: action.payload.id,
        error: null,
      };
    case SET_DOCUNITS:
      return {
        ...state,
        units: action.payload,
        loading: false,
      };
    case SET_CURRENTFILE:
      return {
        ...state,
        current: action.payload,
        loading: false,
      };
    case SET_DOCLOADING:
      return {
        ...state,
        loading: true,
      };
    case GET_DOC:
      return {
        ...state,
        id: action.payload.id,
        units: action.payload.units,
        edges: action.payload.edges,
        nodes: action.payload.nodes,
        loading: false,
      };
    case SET_FITTODATA:
      return {
        ...state,
        fittodataBTN: action.payload,
      };
    case SET_ZOOM:
      return {
        ...state,
        zoom: action.payload,
      };
    case SET_HELP:
      return {
        ...state,
        help: action.payload,
      };
    case ADD_EDGE:
      return {
        ...state,
        edges: action.payload,
      };
    case SET_CURRENTELEMENT:
      return {
        ...state,
        document: action.payload.document,
        hasChanged: action.payload.change,
      };
    case SET_ERROR:
      return {
        ...state,
        error: action.payload,
      };
    case CLEAR_DOC:
      return {
        ...state,
        error: null,
        edges: [],
        nodes: [],
        units: [],
      };

    default:
      return state;
  }
};
