import React, { useReducer } from "react";
//import config from "react-global-configuration";
import axios from "axios";
import DesignsContext from "./designsContext";
import designsReducer from "./designsReducer";
import AWS from "aws-sdk";
import config from "react-global-configuration";
//import config from "react-global-configuration";

import {
  GET_FILES,
  SET_CURRENTFILE,
  SET_FILELOADING,
  GET_SIMULATIONS,
} from "../types";

const DesignsState = (props) => {
  const initialState = {
    folder: -1,
    files: [],
    current: null,
    simulations: null,
    loading: false,
    error: null,
  };

  const [state, dispatch] = useReducer(designsReducer, initialState);
  //----------------------------------------------------------------------

  //-- login user --------------------------------------------------------
  const getFiles = async (folderID) => {
    try {
      //`${config.get("backendURL")}/doc/${folderID}`
      const res = await axios.get(
        `${config.get("backendURL")}/doc?folder=${folderID}`
      );
      let arr = [...res.data.Items];
      arr.forEach((i) => {
        i.date = i.date.replace(/:/, " ");
      });
      arr.sort(function (a, b) {
        return Date.parse(b.date) - Date.parse(a.date);
      });

      dispatch({
        type: GET_FILES,
        payload: {
          folder: folderID,
          files: arr,
          loading: false,
        },
      });
    } catch (err) {
      console.log("error docs >  ", err.response, err);
    }
  };
  //----------------------------------------------------------------------
  const createFile = async (title, description) => {
    dispatch({
      type: SET_FILELOADING,
    });
    try {
      const res = await axios.post(`${config.get("backendURL")}/doc/`, {
        type: 1,
        title,
        description,
      });
      let a = [...state.files];
      a.push({ title, description, key: res.data.id });
      dispatch({
        type: GET_FILES,
        payload: {
          folder: state.folder,
          files: a,
          loading: false,
        },
      });
      dispatch({
        type: SET_CURRENTFILE,
        payload: { payload: res.data.id },
      });
    } catch (err) {
      console.log("error in leftbar >  ", err.response);
    }
  };
  //-- create a file
  //----------------------------------------------------------------------

  //-- Set current file to show the detial
  const setCurrent = (file) => {
    dispatch({ type: SET_CURRENTFILE, payload: file });
  };
  //----------------------------------------------------------------------

  //-- clear erros
  const clearErrors = () => {
    //dispatch({ type: CLEAR_ERRORS });
  };
  //----------------------------------------------------------------------
  const getSimulations = async (docID) => {
    /*const a = [
      {
        key: "1",
        title: "100 cells/HLLC/Beta=0.5",
        dateFinished: "13/11/2021 05:10",
        userExecuted: "Daniel",
        simulationDuration: "10s",
        executionTime: "10h",
        solverType: "CPU",
        serviceType: "Local",
        noOfCores: "10",
        stillRunning: 0.2,
      },
      {
        key: "2",
        title: "with 1000 cells / HLLC",
        dateFinished: "20/10/2021 18:25",
        userExecuted: "Saba",
        simulationDuration: "50s",
        executionTime: "2h",
        solverType: "GPU",
        serviceType: "Local",
        noOfCores: "1",
        stillRunning: 1,
      },
      {
        key: "3",
        title: "with 1000 cells, CFL 0.5",
        dateFinished: "12/10/2021 10:00",
        userExecuted: "Hamid",
        simulationDuration: "500s",
        executionTime: "10h",
        solverType: "CPU",
        serviceType: "Cloud",
        noOfCores: "7",
        stillRunning: 1,
      },
      {
        key: "4",
        title: "with 100 cells",
        dateFinished: "10/10/2021 20:30",
        userExecuted: "Hamid",
        simulationDuration: "10s",
        executionTime: "1h",
        solverType: "GPU",
        serviceType: "Cloud",
        noOfCores: "1",
        stillRunning: -1,
      },
    ];*/
    dispatch({
      type: GET_SIMULATIONS,
      payload: null,
    });
    if (!docID) return;
    try {
      //`${config.get("backendURL")}/doc/${folderID}`
      const res = await axios.get(
        `${config.get("backendURL")}/doc/${docID}/sims`
      );
      dispatch({
        type: GET_SIMULATIONS,
        payload: res.data.Items,
      });
    } catch (err) {
      console.log("error sims >  ", err.response, err, err.response.data);
      if (err.response.data.message === "The incoming token has expired") {
        consol.log("==>", err.response.data.message);
      }
    }
  };
  //----------------------------------------------------------------------
  const deleteDoc = async (opt = 0) => {
    if (state.current) {
      if (state.current.key) {
        //-- ask to delete
        const a = state.files.filter((f) => f.key !== state.current.key);
        if (opt === 0) {
          //-- delete undelete
          try {
            const res = await axios.patch(
              `${config.get("backendURL")}/doc/${state.current.key}?o=10`
            );
            dispatch({
              type: GET_FILES,
              payload: {
                folder: state.folder,
                files: a,
                loading: false,
              },
            });
          } catch (err) {
            console.log("Can not delete document", err.response);
          }
        } else {
          //-- delete permanently
          try {
            const res = await axios.delete(
              `${config.get("backendURL")}/doc/${state.current.key}`
            );
            dispatch({
              type: GET_FILES,
              payload: {
                folder: state.folder,
                files: a,
                loading: false,
              },
            });
          } catch (err) {
            console.log("Can not delete document", err.response);
          }
        }
      }
    }
  };
  //----------------------------------------------------------------------

  return (
    <DesignsContext.Provider
      value={{
        folder: state.folder,
        files: state.files,
        current: state.current,
        loading: state.loading,
        error: state.error,
        simulations: state.simulations,
        clearErrors,
        getFiles,
        deleteDoc,
        setCurrent,
        createFile,
        getSimulations,
      }}
    >
      {props.children}
    </DesignsContext.Provider>
  );
};

export default DesignsState;
