import { MathJax } from "better-react-mathjax";
import React, { Fragment } from "react";
import { Link } from "react-router-dom";
import helpLink from "./HelpUtils";

const EOS_SC = (reqItem, setHelp) => {
  if (reqItem === "title") {
    return "Simple Compressible EOS";
  } else if (reqItem === "text") {
    return (
      <Fragment>
        <p>
          Simple Compressible is the simplest equation of state (EOS) which can
          be used. This EOS reads as
        </p>
        <MathJax>{"\\(P=P_0 + a^2(\\rho-\\rho_0)\\)"}</MathJax>
        <MathJax>
          where {"\\(\\rho\\)"} and {"\\(P\\)"} are the density and pressure.
          The constant ({"\\(\\rho_0\\)"}), ({"\\(P_0\\)"}) and ({"\\(a\\)"})
          are reference density, reference pressure and sound speed,
          respectively {helpLink("EOS_SG", "Stiffened EOS", setHelp)}.
        </MathJax>
      </Fragment>
    );
  } else if (reqItem === "related") {
    return;
  }
};

export default EOS_SC;
