/* eslint-disable */
import React, { useState, useEffect, useContext, Fragment } from "react";
import { Stack, Nav, StackItem, Panel } from "@fluentui/react";
import { Prompt } from "react-router-dom";

import NavBar from "../workspace/NavBar";
import { Interpreter, findItems, makeTabs, initData } from "./Interpreter";

import DocContext from "./../../context/doc/docContext";

import multiMatInputDefinition from "./InputDefinition";
import helpString from "../../assets/help/Help";

const Editor = ({ id, history }) => {
  //=================================================================
  const docContext = useContext(DocContext);
  const { getDoc, document, error, hasChanged, loading } = docContext;
  //=================================================================
  useEffect(() => {
    getDoc(id);
  }, [id]);
  //=================================================================
  useEffect(() => {
    if (error === "WrongID") history.push("/workspace");
  }, [error]);
  //=================================================================
  const [currentTab, setCurrentTab] = useState("");
  //=================================================================
  const [help, setHelp] = useState(null);
  //=================================================================
  const navStyles: Partial<INavStyles> = {
    root: {
      width: 208,
      height: "100%",
      boxSizing: "border-box",
      border: "1px solid #eee",
      overflowY: "auto",
    },
  };
  //=================================================================
  initData(multiMatInputDefinition, "");
  const navLinkGroups = makeTabs(
    multiMatInputDefinition,
    setCurrentTab,
    docContext
  );
  //=================================================================
  /*history.block((targetLocation) => {
    // take your action here
    console.log("Block?");
    return false;
  });*/
  window.onbeforeunload = (event) => {
    const e = event || window.event;
    // Cancel the event
    e.preventDefault();
    let edited = false;
    for (let i in hasChanged) {
      if (hasChanged[i] > 0) edited = true;
    }
    if (edited && !error) {
      if (e) {
        e.returnValue = ""; // Legacy method for cross browser support
      }
      return ""; // Legacy method for cross browser support*/
    }
  };
  //=================================================================
  const exitPrompt = (error, items) => {
    let edited = false;
    for (let i in items) {
      if (items[i] > 0) edited = true;
    }
    if (!error)
      return (
        <Prompt
          when={edited}
          message={() => {
            return "Changes you made may not be saved. Do you want to exit?";
          }}
        />
      );
  };
  //=================================================================
  const loadingSign = (load) => {
    if (load) {
      return (
        <div
          style={{
            position: "absolute",
            zIndex: 1000,
            top: 0,
            left: 0,
            height: window.innerHeight - 10,
            width: "100%",
            background:
              "rgba( 205, 205, 205, 0.4 )   url('spinner.gif') 50% 50% no-repeat",
          }}
        >
          Loading
        </div>
      );
    } else {
      return null;
    }
  };
  //=================================================================
  const mainPage = () => {
    return (
      <Stack
        verticalFill
        styles={{
          root: { height: "100%", width: "100%" },
        }}
      >
        {exitPrompt(error, hasChanged)}
        {loadingSign(loading)}
        <Stack.Item order={1} disableShrink>
          <NavBar isInDesign={true} />
        </Stack.Item>
        <Stack.Item order={2} grow verticalFill>
          <Stack horizontal>
            <Stack.Item order={1}>
              <Nav
                selectedKey="key3"
                styles={navStyles}
                groups={navLinkGroups}
                selectedKey={currentTab}
              />
            </Stack.Item>
            <Stack.Item order={2} grow>
              <div
                style={{
                  height: window.innerHeight - 36,
                  widht: "100%",
                  margin: 10,
                }}
              >
                <Stack
                  styles={{
                    root: {
                      height: "95%",
                      backgroundColor: "#FFFFFF",
                      border: "1px solid #acb9b9",
                      borderRadius: 5,
                      overflowX: "hidden",
                      overflowY: "scroll",
                    },
                  }}
                >
                  <StackItem>
                    <div style={{ height: 20 }}></div>
                  </StackItem>
                  <Stack tokens={{ childrenGap: 5 }}>
                    {currentTab
                      ? Interpreter(
                          //findItems(multiMatInputDefinition, currentTab).items
                          findItems(multiMatInputDefinition, currentTab),
                          -1,
                          docContext,
                          setHelp
                        )
                      : null}
                  </Stack>
                </Stack>
              </div>
            </Stack.Item>
          </Stack>
        </Stack.Item>
        <Panel
          headerText={helpString(help, "title", setHelp)}
          isOpen={help}
          onDismiss={() => setHelp(null)}
          closeButtonAriaLabel="Close"
        >
          {helpString(help, "text", setHelp)}
        </Panel>
      </Stack>
    );
  };
  //=================================================================
  return <Fragment>{document ? mainPage() : null}</Fragment>;
  //=================================================================
};

export default Editor;
