import React, { Fragment, useState, useContext } from "react";
import { Link } from "react-router-dom";
//import { AccountContext } from "../auth/Account";
import AuthContext from "../../context/auth/authContext";
import DocContext from "../../context/doc/docContext";

import DefaultUnits from "../dialogs/DefaultUnits";
import { verifyData } from "../design/Interpreter";
import multiMatInputDefinition from "../design/InputDefinition";

import {
  CommandBarButton,
  Stack,
  TextField,
  ContextualMenuItemType,
} from "@fluentui/react";

import logo from "../../assets/img/logo.png";

const NavBar = ({ isInDesign }) => {
  //------------------------------------------------------------------------------
  const { logout,name } = useContext(AuthContext);
  const docContext = useContext(DocContext);
  //------------------------------------------------------------------------------
  const [commands, setCommands] = useState({
    defaultUnitsDLG: false,
  });
  const { defaultUnitsDLG } = commands;
  //---------------------------------------------------------------------------
  function getLeafNodes(nodes, result = []) {
    //multiMatInputDefinition
    for (const node in nodes) {
      console.log(node, typeof nodes[node]);
      if (typeof nodes[node] === "object") {
        getLeafNodes(nodes[node]);
      }
    }
    return result;
  }
  //---------------------------------------------------------------------------
  function downloadObjectAsJson() {
    verifyData(multiMatInputDefinition, docContext.document);
    return;
    var dataStr =
      "data:text/json;charset=utf-8," +
      encodeURIComponent(JSON.stringify(docContext.document));
    var downloadAnchorNode = document.createElement("a");
    downloadAnchorNode.setAttribute("href", dataStr);
    downloadAnchorNode.setAttribute("download", "input.json");
    document.body.appendChild(downloadAnchorNode); // required for firefox
    downloadAnchorNode.click();
    downloadAnchorNode.remove();
  }
  //------------------------------------------------------------------------------
  const designMenu = {
    items: [
      {
        key: "emailMessage",
        text: "Title/Description",
      },
      {
        key: "unitsEvent",
        text: "Units",
        onClick: () => setCommands({ ...commands, defaultUnitsDLG: true }),
      },
      {
        key: "divider_1",
        itemType: ContextualMenuItemType.Divider,
      },
      {
        key: "simulate",
        text: "Simulate",
        onClick: () => docContext.verifyDocument(),
      },
      {
        key: "divider_2",
        itemType: ContextualMenuItemType.Divider,
      },
      {
        key: "downloadjson",
        text: "Download JSON",
        onClick: () => downloadObjectAsJson(),
      },
      {
        key: "divider_3",
        itemType: ContextualMenuItemType.Divider,
      },
      {
        key: "closedoc",
        text: "Close document",
      },
    ],
  };
  //------------------------------------------------------------------------------
  const helpMenu = {
    items: [
      {
        key: "helpEvent",
        text: "Help",
      },
      {
        key: "tutorialsEvent",
        text: "Tutorials",
      },
      {
        key: "divider1HelpMenu",
        itemType: ContextualMenuItemType.Divider,
      },
      {
        key: "whatsnewEvent",
        text: "What's new",
      },
      {
        key: "reportabugEvent",
        text: "Report a bug",
      },
      {
        key: "featurerequestEvent",
        text: "Feature request",
      },
      {
        key: "divider2HelpMenu",
        itemType: ContextualMenuItemType.Divider,
      },
      {
        key: "aboutEvent",
        text: "About",
        //iconProps: { iconName: "Calendar" },
      },
    ],
    // By default, the menu will be focused when it opens. Uncomment the next line to prevent this.
    // shouldFocusOnMount: false
  };
  //------------------------------------------------------------------------------
  const userMenu = {
    items: [
      {
        key: "menu_myaccount",
        text: "My account",
        iconProps: { iconName: "Settings" },
      },
      {
        key: "menu_supporttickets",
        text: "Support tickets",
        iconProps: { iconName: "Ticket" },
      },
      {
        key: "divider_5",
        itemType: ContextualMenuItemType.Divider,
      },
      {
        key: "menu_signout",
        text: "Sign out",
        iconProps: { iconName: "SignOut" },
        onClick: () => logout(),
      },
    ],
    // By default, the menu will be focused when it opens. Uncomment the next line to prevent this.
    // shouldFocusOnMount: false
  };
  //------------------------------------------------------------------------------
  const closeDLG = (e) => {
    switch (e) {
      case 1:
        setCommands({ ...commands, defaultUnitsDLG: false });
        break;
      default:
        break;
    }
  };
  //------------------------------------------------------------------------------
  const changedText = () => {
    let needSaving = 0;
    for (const i in docContext.hasChanged) {
      if (docContext.hasChanged[i] > 0)
        needSaving = needSaving + docContext.hasChanged[i];
    }
    if (needSaving > 0) {
      if (needSaving > 10) {
        return "needs imediate save";
      } else {
        return docContext.document.title + " [has changed...]";
      }
    } else {
      return docContext.document.title;
    }
  };
  //------------------------------------------------------------------------------
  return (
    <Fragment>
      <Stack
        horizontal
        styles={{ root: { height: "35px", backgroundColor: "#E1DFDD" } }}
      >
        <Stack.Item order={1}>
          <Link to="/">
            <img src={logo} alt="engipipe logo" />
          </Link>
        </Stack.Item>
        {isInDesign ? (
          <Fragment>
            <Stack.Item
              order={2}
              grow
              styles={{
                root: {
                  paddingLeft: 30,
                  paddingRight: "10%",
                  textAlign: "left",
                },
              }}
            >
              <CommandBarButton
                iconProps={{ iconName: "menu-svg" }}
                menuProps={designMenu}
                styles={{
                  root: { height: 35, backgroundColor: "#E1DFDD" },
                  icon: {
                    height: 25,
                    width: 25,
                  },
                }}
              />
            </Stack.Item>
            <Stack.Item
              order={2}
              grow
              styles={{
                root: {
                  paddingLeft: 30,
                  paddingTop: 8,
                  paddingRight: "10%",
                  textAlign: "left",
                },
              }}
            >
              {changedText()}
            </Stack.Item>
          </Fragment>
        ) : (
          <Stack.Item
            order={2}
            grow
            styles={{ root: { paddingLeft: "10%", paddingRight: "10%" } }}
          >
            <TextField
              placeholder="Search in documents ..."
              iconProps={{ iconName: "DocumentSearch" }}
              borderless
              styles={{
                root: {
                  marginTop: 3,
                },
                fieldGroup: {
                  borderRadius: "20px",
                  outline: "none",
                  height: 29,
                  paddingLeft: 10,
                },
              }}
            />
          </Stack.Item>
        )}
        <Stack.Item order={3}>
          <CommandBarButton
            iconProps={{ iconName: "help-svg" }}
            menuProps={helpMenu}
            styles={{
              root: {
                height: 35,
                backgroundColor: "#E1DFDD",
                border: "none",
                outline: "none",
              },
              icon: {
                height: 25,
                width: 25,
              },
            }}
          />
        </Stack.Item>
        <Stack.Item order={4}>
          <CommandBarButton
            iconProps={{ iconName: "user-svg" }}
            text={name}
            menuProps={userMenu}
            styles={{
              root: { height: 35, backgroundColor: "#E1DFDD" },
              icon: {
                height: 25,
                width: 25,
              },
            }}
          />
        </Stack.Item>
      </Stack>
      <DefaultUnits showDLG={defaultUnitsDLG} closeDLG={() => closeDLG(1)} />
    </Fragment>
  );
};

export default NavBar;
