//=================================================================================
const getUnits = (name) => {
  return unitsList.reduce(function (filtered, unit) {
    if (unit.name === name) {
      unit.items.forEach(function (i) {
        filtered.push({ key: i.id, text: i.id });
      });
    }
    return filtered;
  }, []);
};
//=================================================================================
const unitConv = (value, unitFamily, previousUnit, currentUnit) => {
  const a = unitsList.reduce(function (filtered, unit) {
    if (unit.name === unitFamily) {
      filtered.push(unit.items);
    }
    return filtered;
  }, []);
  const fP = a[0].find((element) => element.id === previousUnit);
  const fC = a[0].find((element) => element.id === currentUnit);
  return (value * fP.rate + fP.bias - fC.bias) / fC.rate;
};
//=================================================================================
exports.getUnits = getUnits;
exports.unitConv = unitConv;
