import React, { useState, useEffect, useContext } from "react";
import { Link } from "react-router-dom";
//import { AccountContext } from "./Account";
import AuthContext from "../../context/auth/authContext";

import {
  PrimaryButton,
  Stack,
  TextField,
  FontSizes,
  FontWeights,
  MessageBar,
  MessageBarType,
  Spinner,
  SpinnerSize,
} from "@fluentui/react";
import logo from "../../assets/img/logo.png";

const Login = (props) => {
  //============================================================================
  //const { getSession, authenticate } = useContext(AccountContext);
  const authContext = useContext(AuthContext);
  const { getSession, authenticate } = authContext;
  const [user, setUser] = useState({
    email: "",
    password: "",
    isWaiting: false,
    errorMessage: null,
  });
  //============================================================================
  //-- if we are logged in, it redirects to the main page
  useEffect(() => {
    getSession()
      .then((session) => {
        props.history.push("/workspace");
      })
      .catch((err) => {
        //--
      });
  }, [getSession, props.history]);
  //============================================================================
  const onSubmit = (e) => {
    e.preventDefault();
    if (user.email === "" || user.password === "") return;
    setUser({ ...user, isWaiting: true, errorMessage: null });
    authenticate(user.email, user.password)
      .then((data) => {
        setUser({ ...user, isWaiting: false, errorMessage: null });
        props.history.push("/workspace");
      })
      .catch((err) => {
        if (err.code === "NotAuthorizedException") {
          if (err.message === "Incorrect username or password.") {
            setUser({ ...user, isWaiting: false, errorMessage: err.message });
          }
        } else if (err.code === "UserNotConfirmedException") {
          setUser({
            ...user,
            isWaiting: false,
            errorMessage:
              "Your account needs to be confirmed by the administrator.",
          });
        }
      });
  };
  //============================================================================
  const onChange = (e) => {
    setUser({
      ...user,
      [e.target.name]: e.target.value,
      errorMessage: null,
    });
  };
  //============================================================================
  const errMsg = (
    <MessageBar
      messageBarType={MessageBarType.severeWarning}
      dismissButtonAriaLabel="Close"
      onDismiss={() => setUser({ ...user, errorMessage: null })}
    >
      {user.errorMessage}
    </MessageBar>
  );
  //============================================================================
  const loginBox = (
    <form onSubmit={onSubmit}>
      <Stack
        style={{
          border: "1px solid #c0c0c0",
          borderRadius: "5px",
          padding: "20px",
          marginTop: "10px",
          width: "320px",
        }}
        tokens={{ childrenGap: 10 }}
      >
        {user.errorMessage === null ? null : errMsg}
        <span
          style={{
            fontSize: FontSizes.large,
            fontWeight: FontWeights.bold,
            textAlign: "center",
          }}
        >
          Login
        </span>
        <Stack.Item>
          <TextField
            placeholder="Email"
            name="email"
            onChange={onChange}
            disabled={user.isWaiting}
          />
        </Stack.Item>
        <Stack.Item>
          <TextField
            placeholder="Password"
            type="password"
            name="password"
            onChange={onChange}
            disabled={user.isWaiting}
          />
        </Stack.Item>
        <Stack.Item align="end">
          <span
            style={{
              fontSize: FontSizes.smallPlus,
            }}
          >
            <Link to="/recover">Forget you password?</Link>
          </span>
        </Stack.Item>
        <PrimaryButton
          text="Login"
          type="submit"
          allowDisabledFocus
          checked={true}
          disabled={user.isWaiting}
        />
        {user.isWaiting ? <Spinner size={SpinnerSize.small} /> : null}
        <Stack.Item align="center">
          <Link to="/signup">Register a new user</Link>
        </Stack.Item>
      </Stack>
    </form>
  );
  //============================================================================
  return (
    <Stack
      horizontalAlign="center"
      verticalAlign="stretch"
      style={{ margin: "auto" }}
      grow={true}
      tokens={{ maxWidth: 360, root: { marginTop: "-100px" } }}
    >
      <Stack.Item>
        <div style={{ height: "10px" }} />
      </Stack.Item>
      <Stack.Item>
        <Link to="/">
          <img src={logo} alt="Engipipe Logo" />
        </Link>
      </Stack.Item>
      <Stack.Item>{loginBox}</Stack.Item>
    </Stack>
  );
};

export default Login;
