import React, { useReducer } from "react";
import axios from "axios";
import config from "react-global-configuration";
import DocContext from "./docContext";
import docReducer from "./docReducer";

import multiMatInputDefinition from "../../components/design/InputDefinition";

import {
  SET_DOCUNITS,
  GET_DOC,
  SET_DOCLOADING,
  SET_DOCID,
  SET_FITTODATA,
  SET_TOOLBARSTATE,
  SET_ZOOM,
  ADD_ELEMENTS,
  SET_CONTEXTMENU,
  SET_CURRENTELEMENT,
  SET_ERROR,
  CLEAR_DOC,
  SET_HELP,
  LOAD_DOC,
} from "../types";

const DocState = (props) => {
  const [state, dispatch] = useReducer(docReducer, {
    id: null,
    loading: false,
    error: null,
    units: null,
    help: null,
    hasChanged: {},
    lastSave: null,
    document: null,
  });
  //----------------------------------------------------------------------
  //-- setDOCID ----------------------------------------------------------
  const setDocID = (id) => {
    dispatch({
      type: SET_DOCID,
      payload: id,
    });
    getDoc(id);
  };
  //-- getUnits --------------------------------------------------------
  const getDoc = async (id) => {
    dispatch({ type: SET_DOCLOADING });
    try {
      const res = await axios.get(`${config.get("backendURL")}/doc/${id}`);
      const data = AWS.DynamoDB.Converter.unmarshall(res.data.Items[0]);
      let a = {};
      for (const item in data) {
        a[item] = 0;
      }
      //-- check if the data is not empty
      if (
        data &&
        Object.keys(data).length === 0 &&
        Object.getPrototypeOf(data) === Object.prototype
      ) {
        dispatch({
          type: SET_ERROR,
          payload: "WrongID",
        });
      } else {
        dispatch({
          type: LOAD_DOC,
          payload: { doc: data, changeStructure: a, id },
        });
      }
      //---
    } catch (err) {
      console.log("error docs >  ", err.response, err);
    }
  };
  //----------------------------------------------------------------------
  //--- verify and validate the documents
  const verifyDocument = () => {};
  //----------------------------------------------------------------------
  //-- Set current file to show the detial
  const setLoading = () => {
    dispatch({ type: SET_DOCLOADING });
  };
  //----------------------------------------------------------------------
  const clearDoc = () => {
    dispatch({ type: CLEAR_DOC });
  };
  //----------------------------------------------------------------------

  //-- set/clear erros ---------------------------------------------------
  const setError = (msg) => {
    dispatch({ type: SET_ERROR, payload: msg });
  };
  //----------------------------------------------------------------------
  function resolve(s, o) {
    s = s.replace(/\[(\w+)\]/g, ".$1"); // convert indexes to properties
    s = s.replace(/^\./, ""); // strip a leading dot
    var a = s.split(".");
    for (var i = 0, n = a.length; i < n; ++i) {
      var k = a[i];
      if (k in o) {
        o = o[k];
      } else {
        return;
      }
    }
    return o;
  }
  //----------------------------------------------------------------------
  const setValue = (address, value, unit) => {
    let cpy = { ...state.document };
    //-- be sure about the address
    if (address) {
      //-- if the key exists
      if (!resolve(address, cpy)) {
        //-- if the key doesnt exists
        const lastKey = address.substr(address.lastIndexOf(".") + 1);
        const parentKey = address.substr(0, address.lastIndexOf("."));
        if (resolve(parentKey, cpy)) {
          resolve(parentKey, cpy)[lastKey] = {};
        } else {
          //-- parent is also doesnt exists!
          console.error("Error :: bad data structure1!");
          return;
        }
      }
      //-- now it must be normal
      if (resolve(address, cpy)) {
        if (
          resolve(address, cpy).value !== value ||
          resolve(address, cpy).unit !== unit
        ) {
          let a = address.substr(0, address.indexOf("."));
          if (a.indexOf("[") > 0) a = a.substr(0, address.indexOf("["));
          let b = { ...state.hasChanged };
          b[a] = b[a] + 1;
          console.log("==>", address);
          resolve(address, cpy).value = value;
          resolve(address, cpy).unit = unit;
          dispatch({
            type: SET_CURRENTELEMENT,
            payload: { document: { ...cpy }, change: b },
          });
        }
      } else {
        //-- go for error!
        console.error("Error :: bad data structure2!");
      }
    }
  };
  //----------------------------------------------------------------------
  const addItem = (address, items) => {
    console.log("add ==>" + parent);
    //-- iterate over items and add zero!
    let a = {};
    items.forEach((item, i) => {
      a[item.id] = {
        value: "0",
        unit: "m",
      };
    });
    let cpy = { ...state.document };
    resolve(address, cpy).push(a);
    dispatch({ type: SET_CURRENTELEMENT, payload: { ...cpy } });
  };
  //----------------------------------------------------------------------
  const deleteItem = (address, indx) => {
    let cpy = { ...state.document };
    if (address) {
      resolve(address, cpy).splice(indx, 1);
      dispatch({ type: SET_CURRENTELEMENT, payload: { ...cpy } });
    }
  };
  //----------------------------------------------------------------------
  const saveDoc = async (force = false) => {
    //-- check for save!
    let needSaving = 0;
    for (const i in state.hasChanged) {
      if (state.hasChanged[i] > 0)
        needSaving = needSaving + state.hasChanged[i];
    }
    if ((needSaving > 0 && force) || needSaving > 10) {
      //-- save & reset
      //-- set to saving
      for (const i in state.hasChanged) {
        if (state.hasChanged[i] > 0) {
          //saveDocPart(i);
          try {
            const res = await axios.patch(
              `${config.get("backendURL")}/doc/${state.id}`,
              {
                op: i,
                payload: state.document[i],
              }
            );
            console.log("saving > ", i, JSON.stringify(state.document[i]));
            state.hasChanged[i] = 0;
          } catch (err) {
            console.error("some error ", err);
          }
        }
      }
      //-- remove saving
    }
  };
  //----------------------------------------------------------------------
  const setUnits = (a, b) => {
    console.log("set Units");
  };
  //----------------------------------------------------------------------
  const setHelp = (a) => {
    dispatch({ type: SET_HELP, payload: a });
  };
  //----------------------------------------------------------------------
  const setDefaultUnits = (c) => {
    switch (c) {
      case "SI":
        const u = {
          smallLength: "mm",
          largeLength: "m",
          Velocity: "m/s",
          Pressure: "Pa",
          Angle: "rad",
          Density: "kg/m3",
          Mass: "kg",
          Time: "s",
          Viscosity: "N.s/m2",
          Volume: "m3",
          Temperature: "C",
          pressureGradient: "Pa/m",
          Power: "J/s",
          Frequency: "Hz",
          Compressibility: "kg/m3/Pa",
          Energy: "J",
          massFlowRate: "kg/s",
          molarMass: "g/mol",
          surfaceTension: "N/m",
        };
        dispatch({ type: SET_DOCUNITS, payload: u });
        break;
      default:
        break;
    }
  };
  //----------------------------------------------------------------------

  return (
    <DocContext.Provider
      value={{
        loading: state.loading,
        error: state.error,
        document: state.document,
        units: state.units,
        help: state.help,
        hasChanged: state.hasChanged,
        getDoc,
        setValue,
        addItem,
        deleteItem,
        setUnits,
        setDefaultUnits,
        setHelp,
        clearDoc,
        verifyDocument,
        saveDoc,
      }}
    >
      {props.children}
    </DocContext.Provider>
  );
};

export default DocState;
