/* eslint-disable */
import React, { useState, useContext, Fragment } from "react";
import { Redirect } from "react-router-dom";
import { withRouter } from "react-router-dom";
import DesignsContext from "../../context/designs/designsContext";

//== fluent ui ==========================================
import {
  DocumentCard,
  DocumentCardType,
  DocumentCardPreview,
  DocumentCardDetails,
  DocumentCardTitle,
  DocumentCardActivity,
  StackItem,
} from "@fluentui/react";
import { getTheme } from "@fluentui/react/lib/Styling";

const theme = getTheme();
const { fonts } = theme;
//== fluent ui ==========================================

function Usersingleitem({ history, doc, width, setContextMenu }) {
  //============================================================================
  const designsContext = useContext(DesignsContext);
  const { setCurrent, current } = designsContext;
  //=====================================================================
  const [lastClick, setLastClick] = useState({
    time: -1,
    active: false,
    redirect: false,
  });
  //=====================================================================
  //=== ContextMenu handle
  const handleContextMenu = (e) => {
    e.stopPropagation();
    e.preventDefault();
    if (e.type == "contextmenu") {
      const xPos = e.pageX + "px";
      const yPos = e.pageY + "px";
      setContextMenu({ x: e.pageX, y: e.pageY, id: doc.key });
    }
  };
  //=====================================================================
  const previewPropsUsingIcon: IDocumentCardPreviewProps = {
    previewImages: [
      {
        previewIconProps: {
          iconName: "Precipitation",
          styles: {
            root: { fontSize: fonts.superLarge.fontSize, color: "#FFFFFF" },
          },
        },
        width: 44,
      },
    ],
    styles: { previewIcon: { backgroundColor: "#2A5FAA" } },
  };
  return (
    <StackItem>
      {lastClick.redirect ? <Redirect to={`/doc:${doc._id}`} /> : null}
      <DocumentCard
        aria-label="Document Card with icon. View and share files. Created by Aaron Reid a few minutes ago"
        type={DocumentCardType.compact}
        onClick={(e) => {
          setCurrent(doc);
          e.stopPropagation();
        }}
        onDoubleClick={() => console.log("DBL")}
        onContextMenu={(e) => handleContextMenu(e)}
        styles={{
          root: {
            width: width,
            height: 100,
            textAlign: "left",
            backgroundColor: current
              ? doc.key === current.key
                ? "#E1DFDD"
                : "#fff"
              : "#fff",
            borderRadius: 2,
            border: "1px solid black",
          },
        }}
      >
        <DocumentCardPreview {...previewPropsUsingIcon} />
        <DocumentCardDetails>
          <DocumentCardTitle title={doc.title} />
          <DocumentCardActivity
            activity="Created a few minutes ago"
            people={[
              {
                name: doc.lastEditor,
                initials: "HR", //doc.lastEditor.match(/\b(\w)/g),
                initialsColor: "#2A5FAA",
              },
            ]}
          />
        </DocumentCardDetails>
      </DocumentCard>
    </StackItem>
  );
}

export default withRouter(Usersingleitem);
