/* eslint-disable */
import React, { useState, useContext, useEffect } from "react";
import PropTypes from "prop-types";

import {
  Dialog,
  TextField,
  DefaultButton,
  PrimaryButton,
  DialogFooter,
  DialogType,
} from "@fluentui/react";

const QuestionDialog = ({ title, message, showDLG, closeDLG, what2Do }) => {
  //============================================================================
  const [go, setGo] = useState(false);
  //============================================================================
  const modelProps = {
    isBlocking: false,
  };
  //============================================================================
  const dialogContentProps = {
    type: DialogType.largeHeader,
    title: title,
    subText: message,
  };
  //============================================================================
  const onCancel = () => {
    closeDLG();
  };
  //============================================================================
  const onOK = () => {
    what2Do();
    closeDLG();
  };
  //============================================================================
  return (
    <Dialog
      hidden={!showDLG}
      onDismiss={onCancel}
      dialogContentProps={dialogContentProps}
      modalProps={modelProps}
      minWidth={500}
    >
      <DialogFooter>
        <PrimaryButton onClick={onOK} text="Yes" />
        <DefaultButton onClick={onCancel} text="Cancel" />
      </DialogFooter>
    </Dialog>
  );
};

QuestionDialog.propTypes = {
  showDLG: PropTypes.bool.isRequired,
  closeDLG: PropTypes.func.isRequired,
};

export default QuestionDialog;
