/* eslint-disable */
import {
  REGISTER_SUCCESS,
  REGISTER_FAIL,
  USER_LOADED,
  AUTH_ERROR,
  LOGIN_SUCCESS,
  LOGIN_FAIL,
  LOGOUT,
  CLEAR_ERRORS,
} from "../types";

export default (state, action) => {
  switch (action.type) {
    case LOGIN_SUCCESS:
    case REGISTER_SUCCESS:
      localStorage.setItem("accessToken", action.payload.accessToken);
      return {
        ...state,
        idToken: action.payload.idToken,
        accessToken: action.payload.accessToken,
        sub: action.payload.sub,
        name:action.payload.name,
        isAuthenticated: true,
        loading: false,
      };
    case LOGOUT:
    case LOGIN_FAIL:
    case AUTH_ERROR:
    case REGISTER_FAIL:
      localStorage.removeItem("accessToken");
      return {
        ...state,
        idToken: null,
        accessToken: null,
        sub: null,
        isAuthenticated: false,
        loading: false,
        error: action.payload,
        email: null,
      };
    case CLEAR_ERRORS:
      return {
        ...state,
        error: null,
      };
    case USER_LOADED:
      return {
        ...state,
        isAuthenticated: true,
        loading: false,
      };

    default:
      return state;
  }
};
