////////* eslint-disable */
import React, { useEffect, useState } from "react";
import { getUnits, unitConv } from "./UnitChange";
//import PropTypes from "prop-types";

import {
  Stack,
  TextField,
  DefaultButton,
  Dropdown,
  IconButton,
} from "@fluentui/react";

const DimInput = ({
  title,
  value,
  unit,
  unitFamily,
  inputType,
  onChange,
  singleLine,
  helpID,
  setHelpID,
}) => {
  //============================================================================
  const [dimChange, setDimChange] = useState(false);
  const [numberValue, setNumberValue] = useState(value ? value : "");
  const [unitValue, setUnitValue] = useState(unit);
  const [options, setOptions] = useState([]);
  //============================================================================
  const volume0Icon: IIconProps = { iconName: "Switch" };
  const volume3Icon: IIconProps = { iconName: "Switch" };
  //============================================================================
  useEffect(() => {
    setOptions(getUnits(unitFamily));
    // eslint-disable-next-line
  }, [unitFamily]);
  useEffect(() => {
    onChange(numberValue, unitValue);
    // eslint-disable-next-line
  }, [unitValue]);

  useEffect(() => {
    setNumberValue(value);
    setUnitValue(unit);
    onChange(value, unit);
    // eslint-disable-next-line
  }, [value, unit]);
  //============================================================================
  const dropdownStyles: Partial<IDropdownStyles> = {
    dropdown: { width: 100 },
  };
  //============================================================================
  const onChangeText = (e) => {
    setNumberValue(e.target.value);
  };
  //============================================================================
  const onChangeDropDown = (e, index) => {
    if (dimChange) {
      setNumberValue(unitConv(numberValue, unitFamily, unitValue, index.key));
      setDimChange(false);
    }
    setUnitValue(index.key);
  };
  //============================================================================
  const validateValue = () => {
    if (inputType === "string") return;
    var regexp = /^[+-]?\d+(?:\.\d*(?:[eE][+-]?\d+)?)?$/;
    if (inputType === "integer") {
      regexp = /^([+-]?[1-9]\d*|0)$/;
    }
    if (regexp.test(numberValue)) {
      return;
    } else {
      if (numberValue === "") {
        return;
      } else {
        if (inputType === "integer") {
          return "Invalid number! (expecting integer number)";
        } else {
          return "Invalid number! (expecting real number)";
        }
      }
    }
  };
  //============================================================================
  const unitDropDown = (UV, OP) => {
    return (
      <Stack.Item disableShrink>
        <Dropdown
          placeholder="Select an option"
          options={OP}
          styles={dropdownStyles}
          onChange={onChangeDropDown}
          selectedKey={UV}
          disabled={OP.length === 1 ? true : false}
        />
      </Stack.Item>
    );
  };
  //============================================================================
  const changeUnitButton = (DC) => {
    return (
      <Stack.Item disableShrink>
        <DefaultButton
          iconProps={DC ? volume0Icon : volume3Icon}
          style={{
            width: 30,
            minWidth: 30,
            backgroundColor: DC ? "#2ECC40" : "white",
          }}
          onClick={() => {
            setDimChange(!DC);
          }}
        />
      </Stack.Item>
    );
  };
  //============================================================================
  const normalTitle = (TL, SL, HID) => {
    return (
      <Stack.Item
        disableShrink
        styles={{
          root: {
            width: SL ? 275 : 200,
            alignItems: "top",
            display: "flex",
            justifyContent: "flex-end",
          },
        }}
      >
        <span style={{ fontWeight: 600, paddingTop: 6 }}>{TL + ":"} </span>
        {HID ? (
          <IconButton
            iconProps={{ iconName: "hint-svg" }}
            styles={{ root: { width: 20 } }}
            onClick={() => setHelpID(HID)}
          />
        ) : null}
      </Stack.Item>
    );
  };
  //============================================================================
  const dropDownTitle = (title) => {
    return (
      <Stack.Item
        styles={{
          root: {
            width: 270,
            alignItems: "top",
            display: "flex",
            justifyContent: "flex-end",
          },
        }}
      >
        <span style={{ fontWeight: 600, paddingRight: "10px" }}>
          {title + ":"}{" "}
        </span>
      </Stack.Item>
    );
  };
  //============================================================================
  return (
    <Stack horizontal tokens={{ childrenGap: 5 }}>
      {inputType !== "sectionType"
        ? normalTitle(title, singleLine, helpID)
        : dropDownTitle(title)}
      {value ? (
        <Stack.Item
          styles={{ root: { width: inputType === "string" ? 445 : 120 } }}
          disableShrink
        >
          <TextField
            name={title}
            onChange={onChangeText}
            onBlur={() => {
              if (numberValue !== value) onChange(numberValue, unitValue);
            }}
            value={numberValue}
            validateOnFocusOut={inputType === "string" ? false : true}
            onGetErrorMessage={validateValue}
            autoComplete="off"
          />
        </Stack.Item>
      ) : null}
      {unitFamily ? unitDropDown(unitValue, options) : null}
      {value && unitFamily ? changeUnitButton(dimChange) : null}
    </Stack>
  );
};
//============================================================================
/*FluidProperties.propTypes = {
  showDLG: PropTypes.bool.isRequired,
  closeDLG: PropTypes.func.isRequired,
};*/

export default DimInput;
