import React, { useContext, useEffect, useState } from "react";
import DesignsContext from "../../context/designs/designsContext";
import Simulate from "../dialogs/Simulate";

import {
  StackItem,
  ScrollablePane,
  Stack,
  Sticky,
  StickyPositionType,
  PrimaryButton,
  Text,
  ActionButton,
  ProgressIndicator,
} from "@fluentui/react";
import { mergeStyleSets } from "@fluentui/react/lib/Styling";

function Simulations() {
  //============================================================================
  const designsContext = useContext(DesignsContext);
  const { current, getSimulations, simulations, folder } = designsContext;
  //===========================================================================
  const [simulateDLG, setSimulateDLG] = useState(false);
  //===========================================================================
  useEffect(() => {
    if (current) {
      getSimulations(current.key);
    } else {
      getSimulations(null);
    }
    // eslint-disable-next-line
  }, [current]);
  //===========================================================================
  //===========================================================================

  const classNames = mergeStyleSets({
    wrapper: {
      height: "100%",
      position: "relative",
    },
    pane: {
      maxWidth: "100%",
      border: "1px solid #edebe9",
    },
    sticky: {
      color: "#201f1e",
      padding: "5px 20px 5px 10px",
      borderTop: "1px solid #000000",
      borderBottom: "1px solid #000000",
    },
    textContent: {
      padding: "5px 10px",
    },
  });
  const scrollablePaneStyles: Partial<IScrollablePaneStyles> = {
    root: classNames.pane,
  };
  //const colors = ["#c7e0f4", "#71afe5", "#deecf9"];
  /*const items = Array.from({ length: 6 }).map((item, index) => ({
    color: colors.splice(Math.floor(Math.random() * colors.length), 1)[0],
    text: lorem(200),
    index,
  }));*/

  //===========================================================================
  const CGLCN = (item) => {
    return (
      <StackItem
        styles={{
          root: {
            background: "#0f9960",
            borderRadius: 2,
            paddingTop: 2,
            paddingBottom: 2,
            paddingLeft: 5,
            paddingRight: 5,
            color: "#fff",
            height: 19,
          },
        }}
      >
        <Text styles={{ root: { color: "white" } }}>{item}</Text>
      </StackItem>
    );
  };
  //===========================================================================
  const createContentArea = (item, index) => (
    <div
      key={item.key}
      style={{
        backgroundColor: item.color,
      }}
    >
      <Sticky stickyPosition={StickyPositionType.Both}>
        <div role="heading" aria-level={1} className={classNames.sticky}>
          <Stack horizontal grow>
            <StackItem align="start">
              <Text variant="medium">Simulation {index + 1}</Text>
            </StackItem>
            <StackItem
              align="end"
              styles={{ root: { textAlign: "right" } }}
              grow
            >
              <Text variant="smallPlus">{item.dateFinished}</Text>
            </StackItem>
          </Stack>
        </div>
      </Sticky>
      <div className={classNames.textContent}>
        <Stack>
          <Stack horizontal grow>
            <StackItem>
              <Text styles={{ root: { fontWeight: 600 } }}>Title:</Text>
            </StackItem>
            <StackItem grow styles={{ root: { textAlign: "left" } }}>
              {item.title}
            </StackItem>
          </Stack>
          <Stack horizontal grow>
            <StackItem>
              <Text styles={{ root: { fontWeight: 600 } }}>
                Simulation Duration :
              </Text>
            </StackItem>
            <StackItem grow styles={{ root: { textAlign: "left" } }}>
              {item.simulationDuration}
            </StackItem>
            <StackItem>
              <Text styles={{ root: { fontWeight: 600 } }}>
                Execution Time :
              </Text>
            </StackItem>
            <StackItem grow styles={{ root: { textAlign: "left" } }}>
              {item.executionTime}
            </StackItem>
          </Stack>
          <Stack
            horizontal
            tokens={{ childrenGap: 5 }}
            styles={{ root: { height: 27, paddingTop: 5 } }}
          >
            {CGLCN(item.solverType + " [" + item.noOfCores + "]")}
            {CGLCN(item.serviceType)}
            {item.stillRunning > 0 && item.stillRunning < 1 ? (
              <StackItem grow>
                <ProgressIndicator
                  styles={{
                    itemProgress: { paddingTop: 0 },
                    itemName: { paddingTop: 0 },
                  }}
                  label="Running..."
                  percentComplete={item.stillRunning}
                />
              </StackItem>
            ) : null}
          </Stack>
          <Stack horizontal grow>
            <ActionButton
              text="View reults"
              iconProps={{ iconName: "Chart" }}
            />
            <ActionButton
              text="Finish simulation"
              iconProps={{ iconName: "Rerun" }}
            />
            <StackItem align="end" grow styles={{ root: { align: "right" } }}>
              <ActionButton
                iconProps={{ iconName: "EntryView" }}
                ariaLabel="view info"
              />
              <ActionButton
                iconProps={{ iconName: "ReportWarning" }}
                ariaLabel="view errors"
              />
              <ActionButton
                iconProps={{ iconName: "Download" }}
                ariaLabel="download simulation"
              />
              <ActionButton
                iconProps={{ iconName: "Delete" }}
                ariaLabel="delete simulation"
              />
            </StackItem>
          </Stack>
        </Stack>
      </div>
    </div>
  );
  //===========================================================================
  return (
    <Stack
      grow
      styles={{
        root: {
          backgroundColor: "#E1DFDD",
          height: "100%",
        },
      }}
    >
      <Simulate
        showDLG={simulateDLG}
        closeDLG={() => setSimulateDLG(!simulateDLG)}
      />
      <StackItem className="detailTitle">
        <span>Simulations</span>
      </StackItem>
      <StackItem align="center">
        {folder === "2" || folder === "3" ? null : (
          <PrimaryButton
            text="New simulation"
            iconProps={{ iconName: "Play" }}
            styles={{
              root: {
                margin: "5px",
                height: 25,
              },
            }}
            onClick={() => setSimulateDLG(true)}
            disabled={current ? (current === "" ? true : false) : true}
          />
        )}
      </StackItem>
      <StackItem
        styles={{
          root: {
            overflowY: "hiden",
            overflowX: "hidden",
            height: "100%",
          },
        }}
      >
        <div className={classNames.wrapper}>
          <ScrollablePane
            scrollContainerFocus={true}
            scrollContainerAriaLabel="Sticky component example"
            styles={scrollablePaneStyles}
          >
            {simulations
              ? simulations.map((item, index) => {
                  return createContentArea(item, index);
                })
              : null}
          </ScrollablePane>
        </div>
      </StackItem>
    </Stack>
  );
}
//            {...contentAreas}

export default Simulations;
