import EOS_SC from "./EOS_SC";
import EOS_SG from "./EOS_SG";

const helpString = (helpID, reqItem, setHelp) => {
  switch (helpID) {
    case "EOS_SC":
      return EOS_SC(reqItem, setHelp);
    case "EOS_SG":
      return EOS_SG(reqItem, setHelp);
    default:
      return;
  }
};
export default helpString;
