import React, { useState, useEffect } from "react";
import { Stack, StackItem, IconButton } from "@fluentui/react";
import { VictoryChart, VictoryLine, VictoryTheme, VictoryGroup } from "victory";
import { Resizable } from "re-resizable";
import SettingsDLG from "./SettingsDLG";

import testData from "./testData";

function Graph() {
  //============================================================================
  const [showSettings, setShowSettings] = useState(false);
  //-----------------------------------------------------------------------------------
  const [dim, setdim] = useState({
    height: 350,
    width: 500,
    visible: true,
  });
  //-----------------------------------------------------------------------------------
  useEffect(() => {
    console.log("==>", dim);
    // eslint-disable-next-line
  }, [dim]);
  //-----------------------------------------------------------------------------------
  const resizeableStyle = {
    display: "flex",
    alignItems: "center",
    justifyContent: "center",
    border: "solid 1px black",
    background: "#f0f0f0",
  };
  //-----------------------------------------------------------------------------------
  return (
    <Stack.Item styles={{ root: { padding: 10 } }}>
      <SettingsDLG
        showDLG={showSettings}
        closeDLG={() => setShowSettings(false)}
      />
      <Resizable
        defaultSize={{ wigth: dim.width, height: dim.height }}
        style={resizeableStyle}
        minHeight="350"
        minWidth="500"
        onResizeStart={(e, direction, ref, d) => {
          setdim({
            ...dim,
            visible: false,
          });
        }}
        onResizeStop={(e, direction, ref, d) => {
          /*setdim({
            height: parseInt(Math.max(dim.height + d.height, 350)),
            width: parseInt(Math.max(dim.width + d.width, 500)),
            visible: true,
          });*/
          setdim({
            height: parseInt(Math.max(dim.height + d.height, 350)),
            width: parseInt(Math.max(dim.width + d.width, 500)),
            visible: true,
          });
        }}
      >
        <Stack horizontal>
          <Stack verticalAlign="center">
            <IconButton
              iconProps={{ iconName: "Settings" }}
              onClick={() => setShowSettings(true)}
            />
            <IconButton
              iconProps={{ iconName: "ExploreData" }}
              onClick={() => console.log("add/delete new curve/color/legend")}
            />
            <IconButton
              iconProps={{ iconName: "Slideshow" }}
              onClick={() =>
                console.log("Animate same thing in bunch of files?")
              }
            />
            <IconButton
              iconProps={{ iconName: "DrillDown" }}
              onClick={() => console.log("Download PNG/PDF")}
            />
            <IconButton
              iconProps={{ iconName: "Delete" }}
              onClick={() => console.log("delete graph")}
            />
          </Stack>
          <StackItem>
            {dim.visible ? (
              <VictoryChart
                height={dim.height - 5}
                width={dim.width - 32}
                domain={{ x: [0, 12], y: [0, 1] }}
              >
                <VictoryGroup
                  events={[
                    {
                      target: "parent",
                      eventHandlers: {
                        onClick: () => {
                          console.log("click");
                        },
                        onMouseOver: () => {
                          console.log("over");
                        },
                        onMouseOut: () => {
                          console.log("out");
                        },
                      },
                    },
                  ]}
                >
                  <VictoryLine
                    style={{
                      data: { stroke: "#c43a31", strokeWidth: 15 },
                      parent: { border: "1px solid #ccc" },
                    }}
                    data={testData.data}
                    domain={{
                      x: [testData.xmin, testData.xmax],
                      y: [testData.ymin, testData.ymax],
                    }}
                    name={testData.title}
                  />
                </VictoryGroup>
              </VictoryChart>
            ) : null}
          </StackItem>
        </Stack>
      </Resizable>
    </Stack.Item>
  );
}
//scale={{ x: "linear", y: "log" }}
//VictoryChart theme={VictoryTheme.material}

export default Graph;
