/* eslint-disable */
import React, { useContext, Fragment, useState, useEffect } from "react";
import { useHistory } from "react-router-dom";
import DesignsContext from "../../context/designs/designsContext";
import QuestionDialog from "../dialogs/QuestionDialog";

/*Title / Description / Created by / Shared with / Last modified / image
Open / delete / share*/

import {
  Stack,
  Text,
  StackItem,
  DefaultButton,
  PrimaryButton,
} from "@fluentui/react";
import Simulations from "./Simulations";

/*import {
  Card,
  ICardTokens,
  ICardSectionStyles,
  ICardSectionTokens,
} from "@uifabric/react-cards";*/

const Detail = () => {
  //============================================================================
  const history = useHistory();
  //============================================================================
  const designsContext = useContext(DesignsContext);
  const { current, folder } = designsContext;
  //===========================================================================
  const [docID, setDocID] = useState();
  //===========================================================================
  const [dialogSettings, setDialogSettings] = useState({
    title: "Delete Document",
    message: "Do you want to delete the current document?",
    what2Do: 0,
    show: false,
  });
  //===========================================================================
  useEffect(() => {
    //if (docID) history.push(`/editor/${current.key}`);
    if (docID) {
      const win = window.open(`#/editor/${current.key}`, "_blank");
      win.focus();
    }
    //if (docID) history.push(`/editor`);
    // eslint-disable-next-line
  }, [docID]);
  //==============================================================================
  const _overflowItems = (f) => {
    return [
      {
        key: "rename",
        text: "Rename...",
        onClick: () => console.log("Rename"),
        iconProps: { iconName: "Rename" },
        disabled: f === 1 || f === 2 ? true : false,
      },
      {
        key: "copyto",
        text: "Copy to",
        iconProps: { iconName: "Copy" },
        onClick: () => console.log("Copy"),
        renderedInOverflow: true,
      },
      {
        key: "download",
        text: "Download",
        onClick: () => console.log("Copy to"),
        iconProps: { iconName: "Download" },
      },
      {
        key: "upload",
        text: "Upload",
        onClick: () => console.log("Copy to"),
        iconProps: { iconName: "Upload" },
        disabled: f === 1 || f === 2 ? true : false,
      },
      {
        key: "delete",
        text: "Delete",
        onClick: () =>
          setDialogSettings({
            title: "Delete Document",
            message: "Do you want to delete the current document?",
            what2Do: 0,
            show: true,
          }),
        iconProps: { iconName: "MoveToFolder" },
        disabled: f === 1 || f === 2 ? true : false,
      },
    ];
  };

  const overflowProps: IButtonProps = {
    ariaLabel: "More commands",
    iconProps: { iconName: "Copy" },
  };

  //==============================================================================

  return (
    <Fragment>
      <QuestionDialog
        title={dialogSettings.title}
        message={dialogSettings.message}
        showDLG={dialogSettings.show}
        closeDLG={() =>
          setDialogSettings({
            title: "Delete Document",
            message: "Do you want to delete the current document?",
            what2Do: 0,
            show: !dialogSettings.show,
          })
        }
        what2Do={() => designsContext.deleteDoc(dialogSettings.what2Do)}
      />
      <Stack
        grow
        styles={{
          root: {
            height: "100%",
            backgroundColor: "#E1DFDD",
          },
        }}
      >
        <Stack styles={{ root: { height: 210 } }}>
          <StackItem className="detailTitle">
            <span>Document Details</span>
          </StackItem>
          <StackItem align="start" className="detailCell">
            <span className="detailTopic">Title: </span>
            <span>{current ? current.title : null}</span>
          </StackItem>
          <StackItem
            align="start"
            className="detailCell"
            styles={{ root: { height: 80 } }}
          >
            <span className="detailTopic">Description: </span>
            <span>{current ? current.description : null}</span>
          </StackItem>
          <StackItem align="start" styles={{ root: { marginBottom: 10 } }}>
            <span className="detailTopic">Created by Hamid at 2020/02/20</span>
            <br />
            <span className="detailTopic">
              Last edit by Hamid at 2020/02/20
            </span>
          </StackItem>
          <StackItem>
            {folder === 3 ? null : (
              <PrimaryButton
                text={folder === 2 ? "View" : "Edit"}
                iconProps={{ iconName: "PageHeaderEdit" }}
                styles={{
                  root: {
                    backgroundColor: "#0f9960",
                    borderColor: "#0f9960",
                    marginLeft: "5px",
                    height: 25,
                  },
                  rootHovered: {
                    backgroundColor: "#0b7448",
                  },
                  rootPressed: {
                    backgroundColor: "#095d3a",
                  },
                }}
                onClick={() => {
                  setDocID(current.key);
                }}
                disabled={current ? (current == "" ? true : false) : true}
              />
            )}
            {folder === 3 ? null : (
              <DefaultButton
                text="Doc. managment"
                iconProps={{ iconName: "BulletedList" }}
                styles={{
                  root: {
                    marginLeft: "5px",
                    height: 25,
                  },
                }}
                menuProps={{ items: _overflowItems(folder) }}
                disabled={current ? (current == "" ? true : false) : true}
              />
            )}
            {folder === 1 || folder === 2 || folder === 3 ? null : (
              <PrimaryButton
                text="Share"
                iconProps={{ iconName: "UserRemove" }}
                styles={{
                  root: {
                    marginLeft: "5px",
                    height: 25,
                  },
                }}
                disabled={current ? (current == "" ? true : false) : true}
              />
            )}
            {folder === 3 ? (
              <Fragment>
                <PrimaryButton
                  text="Undelete"
                  iconProps={{ iconName: "EmptyRecycleBin" }}
                  styles={{
                    root: {
                      marginLeft: "5px",
                      height: 25,
                    },
                  }}
                  disabled={current ? (current == "" ? true : false) : true}
                  onClick={() => designsContext.deleteDoc(0)}
                />
                <DefaultButton
                  text="Delete permanently"
                  iconProps={{ iconName: "delete" }}
                  styles={{
                    root: {
                      marginLeft: "5px",
                      height: 25,
                    },
                  }}
                  disabled={current ? (current == "" ? true : false) : true}
                  onClick={() => {
                    setDialogSettings({
                      title: "Delete Document",
                      message:
                        "Do you want to permanently delete the current document?",
                      what2Do: 1,
                      show: !dialogSettings.show,
                    });
                  }}
                />
              </Fragment>
            ) : null}
          </StackItem>
        </Stack>
        <StackItem
          grow
          styles={{
            root: {
              backgroundColor: "#555556",
            },
          }}
        >
          <Simulations />
        </StackItem>
      </Stack>
    </Fragment>
  );
};

export default Detail;
