import React from "react";
//import { BrowserRouter as Router, Route, Switch } from "react-router-dom";
import { HashRouter as Router, Route, Switch } from "react-router-dom";
import { MathJaxContext } from "better-react-mathjax";
//import { Account } from "./components/auth/Account";
import AuthState from "./context/auth/AuthState";
import DesignsState from "./context/designs/DesignsState";
import DocState from "./context/doc/DocState";
import PrivateRoute from "./components/routing/PrivateRoute";

import "./App.css";
import RegisterIcons from "./components/icons/RegisterIcons";

import Login from "./components/auth/Login";
import WorkSpace from "./components/workspace/WorkSpace";
import Visualize from "./components/results/Visualize";
import Editor from "./components/design/Editor";
import UserPanel from "./components/controlpanel/UserPanel";
import RecoverPassword from "./components/auth/RecoverPassword";
import SignUp from "./components/auth/SignUp";

// eslint-disable-next-line
import apiClient from "./utils/axiosInterceptor";

import config from "react-global-configuration";
import configuration from "./config/default";
config.set(configuration);

function App() {
  //============================================
  RegisterIcons();
  //============================================
  return (
    <MathJaxContext>
      <div className="App">
        <AuthState>
          <DesignsState>
            <Router basename={config.get("homepage")}>
              <Switch>
                <Route exact path="/login" component={Login} />
                <Route exact path="/signup" component={SignUp} />
                <Route exact path="/recover" component={RecoverPassword} />
                <Route exact path="/" component={Login} />
                <DocState>
                  <PrivateRoute exact path="/workspace" component={WorkSpace} />
                  <PrivateRoute
                    exact
                    path="/editor/:docID"
                    component={(props) => (
                      <Editor id={props.match.params.docID} {...props} />
                    )}
                    getUserConfirmation={(message, callback) => {
                      // this is the default behavior
                      //const allowTransition = window.confirm(message);
                      //callback(allowTransition);
                      callback(() => {
                        console.error(message);
                      });
                    }}
                  />
                  <PrivateRoute exact path="/visualize" component={Visualize} />
                </DocState>
                <PrivateRoute exact path="/userpanel" component={UserPanel} />
              </Switch>
            </Router>
          </DesignsState>
        </AuthState>
      </div>
    </MathJaxContext>
  );
}

export default App;
