import React from "react";
import {
  Stack,
  StackItem,
  Dropdown,
  CommandBar,
  DefaultButton,
  getHighContrastNoAdjustStyle,
} from "@fluentui/react";
import DimInput from "../input/DimInput";

function Interpreter(inp, idx = -1, docContext, setHelp) {
  const items = inp.items ? inp.items : inp.variables;
  //--------------------------------------------------------------------------------------------
  //const docContext = useContext(DocContext);
  const { document, setValue, addItem, deleteItem, units } = docContext;
  //--------------------------------------------------------------------------------------------
  const onChangeUnit = (v, u, t, q) => {
    //-- it is edit box
    setValue(q, v, u);
  };
  const onChangeDropDown = (v, u, t, q) => {
    //-- it is a drop box
    setValue(q, u.key, "");
  };
  //--------------------------------------------------------------------------------------------
  function resolve(s, o) {
    s = s.replace(/\[(\w+)\]/g, ".$1"); // convert indexes to properties
    s = s.replace(/^\./, ""); // strip a leading dot
    var a = s.split(".");
    for (var i = 0, n = a.length; i < n; ++i) {
      var k = a[i];
      if (k in o) {
        o = o[k];
      } else {
        return;
      }
    }
    return o;
  }
  //--------------------------------------------------------------------------------------------
  const onAddItem = (p, itms) => {
    let go = true;
    if (itms.fixed) go = false;
    //if (resolve(p, document).length >= itms.maxItems) go = false;
    if (go) addItem(p, itms.items);
  };
  //--------------------------------------------------------------------------------------------
  const onDeleteItem = (p, indx) => {
    deleteItem(p, indx);
  };
  //--------------------------------------------------------------------------------------------
  let itemList = [];

  items.forEach((item, index) => {
    //--- if the iten is single line
    const singleLineStyles = {
      root: {
        width: item.singleLine
          ? window.innerWidth - 300
          : (window.innerWidth - 300) / 2,
        minWidth: 300,
      },
    };
    //--- if the item is an array we need to correct
    let address = item.parent + "." + item.id;
    let parentAddress = item.parent;
    if (idx !== -1) {
      const s = item.parent.split(".").slice(0, -1).join(".");
      //address = s + `\[${idx}\]` + "." + item.id;
      //address = s + `[${idx}].${item.id}`;
      if (!item.category) address = s + `[${idx}].${item.id}`;
      else address = s + `[${idx}].${item.category}.${item.id}`;
      //parentAddress = s + `\[${idx}\]`;
      parentAddress = s + `[${idx}]`;
    }
    //--- visibility check
    let visibleItem = false;
    if (item.visibilityGroup) {
      let skey = resolve(parentAddress + "." + item.visibilityGroup, document)
        .value;
      if (item.visibilityKey.includes(skey)) visibleItem = true;
    } else {
      visibleItem = true;
    }
    //---
    switch (item.type) {
      case "selection":
        if (visibleItem)
          itemList.push(
            <StackItem styles={singleLineStyles} grow key={address}>
              <Stack horizontal tokens={{ childrenGap: 5 }}>
                <Stack.Item
                  styles={{
                    root: {
                      width: 275,
                      alignItems: "top",
                      display: "flex",
                      justifyContent: "flex-end",
                    },
                  }}
                >
                  <span style={{ fontWeight: 600, paddingTop: "7px" }}>
                    {item.id + ":"}
                  </span>
                </Stack.Item>
                <Stack.Item styles={{ root: { width: 200 } }}>
                  <Dropdown
                    placeholder="Select options"
                    options={item.options}
                    selectedKey={
                      resolve(address, document)
                        ? resolve(address, document).value
                        : null
                    }
                    disabled={
                      item.options.length > 1 && !item.fixed ? false : true
                    }
                    onChange={(e, u) => onChangeDropDown(e, u, "", address)}
                  />
                </Stack.Item>
              </Stack>
            </StackItem>
          );
        break;
      case "group":
        itemList.push(
          <StackItem key={address}>
            <Stack
              tokens={{ childrenGap: 2 }}
              styles={{
                root: {
                  border: "1px solid black",
                  borderRadius: 3,
                  marginLeft: 10,
                  marginRight: 10,
                  paddingTop: 20,
                  paddingBottom: 10,
                },
              }}
            >
              <DimInput
                title={item.title ? item.title : item.id}
                onChange={(v, u) => onChangeUnit(v, u, "largeLength")}
                inputType="sectionType"
                helpID={item.helpID}
              />
              {Interpreter(item, -1, docContext, setHelp)}
            </Stack>
          </StackItem>
        );
        break;
      case "list":
        //item.data.map((row) => {
        itemList.push(
          resolve(item.parent, document).map((row, index) => {
            return (
              <StackItem key={address + index}>
                <CommandBar
                  farItems={[
                    {
                      iconOnly: true,
                      iconProps: { iconName: "Delete" },
                      disabled: item.fixed ? true : false,
                      onClick: () => onDeleteItem(item.parent, index),
                      key: `${item.id} ${index + 1}+BTN`,
                    },
                  ]}
                  items={[
                    {
                      disabled: true,
                      text: `${item.id} ${index + 1}`,
                      key: `${item.id} ${index + 1}`,
                    },
                  ]}
                  styles={{
                    root: {
                      height: 15,
                      marginBottom: 2,
                    },
                  }}
                />
                <Stack
                  horizontal
                  wrap
                  tokens={{ childrenGap: 5 }}
                  styles={{
                    root: {
                      border: "1px solid black",
                      borderRadius: 3,
                      marginLeft: 10,
                      marginRight: 10,
                      paddingTop: 20,
                      paddingBottom: 10,
                    },
                  }}
                >
                  {Interpreter(item, index, docContext, setHelp)}
                </Stack>
              </StackItem>
            );
          })
        );
        itemList.push(
          <DefaultButton
            key={address + "BTN"}
            text={"Add new " + item.id}
            iconProps={{ iconName: "Add" }}
            style={{
              backgroundColor: item.fixed ? "#bdc1c1" : "#0f9960",
              color: "#fff",
              marginTop: 10,
              marginRight: "auto",
              marginLeft: "auto",
            }}
            disabled={item.fixed ? true : false}
            onClick={() => onAddItem(item.parent, item)}
          />
        );
        break;
      default:
        //styles={{ root: { display: visibleItem } }}
        if (visibleItem)
          itemList.push(
            <StackItem styles={singleLineStyles} grow key={address}>
              <DimInput
                unit={
                  resolve(address, document)
                    ? resolve(address, document).unit
                      ? resolve(address, document).unit
                      : item.unitFamily
                      ? units[item.unitFamily]
                      : ""
                    : ""
                }
                unitFamily={item.unitFamily}
                title={item.title ? item.title : item.id}
                value={
                  resolve(address, document)
                    ? resolve(address, document).value
                    : "0"
                }
                onChange={(v, u) =>
                  onChangeUnit(v, u, item.unitFamily, address)
                }
                inputType={item.inputType ? item.inputType : null}
                singleLine={item.singleLine}
                helpID={item.helpID}
                setHelpID={setHelp}
              />
            </StackItem>
          );
        break;
    }
  });
  return itemList;
}
/*              {
                document.PhysicalProperties.SurfaceTension.value
                  ? document.PhysicalProperties.SurfaceTension.value
                  : null
              }*/
//============================================================================================
const findItems = (items, key) => {
  const found = items.find((item) => item.key === key);
  return found;
};
//============================================================================================
const makeTabs = (items, setCurrentTab, docContext) => {
  let a = [];
  items.forEach((item) => {
    a.push({
      name: item.title ? item.title : item.id,
      key: item.key,
      icon: item.icon,
      isExpanded: true,
      onClick: () => {
        if (item.id) docContext.saveDoc(true);
        setCurrentTab(item.key);
      },
    });
  });
  return [{ links: a }];
};
//============================================================================================
const initData = (input, parent = "") => {
  input.forEach((item) => {
    item.parent = parent;
    if (item.items) {
      initData(
        item.items,
        item.parent === "" ? item.id : item.parent + "." + item.id
      );
    }
  });
};
//============================================================================================
const verifyData = (def, data, result = [], st = 0) => {
  //let parents = [];
  //let cData = { ...data };
  if (st === 2) return;
  for (const item in data) {
    if (st === 1) console.log(item);
    if (!data[item].value || !data[item].unit) {
      if (typeof data[item] === "string") {
        result[item] = data[item];
      } else if (typeof data[item].length === "undefined") {
        result[item] = {};
        verifyData(def, data[item], result, st + 1);
      } else {
        result[item] = [];
        verifyData(def, data[item], result, st + 1);
      }
    } else {
      result[item] = data[item].value;
    }
  }
  if (st === 0) console.log(result);
};
//============================================================================================

export { Interpreter, findItems, makeTabs, initData, verifyData };
