import React, { useContext, useState } from "react";
import Usersingleitem from "./Usersingleitem";
import DesignsContext from "../../context/designs/designsContext";
import Simulate from "../dialogs/Simulate";

import {
  Stack,
  Text,
  FontIcon,
  IContextualMenuProps,
  ContextualMenuItemType,
  ContextualMenu,
} from "@fluentui/react";
import { Icon } from "@fluentui/react/lib/Icon";
import { mergeStyleSets } from "@fluentui/react/lib/Styling";
import { getTheme } from "@fluentui/react/lib/Styling";
import { ContextMenu } from "@blueprintjs/core";

const theme = getTheme();

//###############################################################################################################
function Useritems() {
  //============================================================================
  const designsContext = useContext(DesignsContext);
  const { files, folder, setCurrent } = designsContext;
  //============================================================================
  //---------------------------------------------------------------------------
  const dateTextStyles: ITextStyles = {
    root: {
      color: "#505050",
      fontWeight: 600,
      margin: "auto",
    },
  };
  //---------------------------------------------------------------------------
  const iconClass = {
    fontSize: 25,
    height: 25,
    width: 25,
    margin: "7px 12px 0px 10px",
  };
  //---------------------------------------------------------------------------
  //=====================================================================
  const [dlgs, setDlgs] = useState({ simulateDLG: false });
  const [contextMenu, setContextMenu] = useState(null);
  //=====================================================================
  const classNames = mergeStyleSets({
    iconContainer: {
      position: "relative",
      margin: "0 4px",
      height: 32,
      width: 14,
    },
    logoIcon: {
      position: "absolute",
      left: 0,
      right: 0,
      color: theme.palette.themeDarkAlt,
    },
    logoFillIcon: {
      position: "absolute",
      left: 0,
      right: 0,
      color: theme.palette.white,
    },
  });

  const menuProps: IContextualMenuProps = {
    shouldFocusOnMount: true,
    items: [
      {
        key: "editDocument",
        text: "Edit",
        iconProps: { iconName: "PageHeaderEdit" },
        onClick: () => {
          setContextMenu();
          const win = window.open(`#/editor/${contextMenu.id}`, "_blank");
          win.focus();
        },
      },
      {
        key: "editDocument1",
        text: "Simulate",
        iconProps: { iconName: "Play" },
        onClick: () => {
          setContextMenu();
          setDlgs({ ...dlgs, simulateDLG: true });
        },
      },
      {
        key: "editDocument2",
        text: "Visualize results",
        iconProps: { iconName: "HistoricalWeather" },
      },
      { key: "divider_1", itemType: ContextualMenuItemType.Divider },
      {
        key: "share",
        iconProps: { iconName: "TextDocumentShared" },
        text: "Share",
      },
      { key: "rename", iconProps: { iconName: "Rename" }, text: "Rename" },
      { key: "copyto", iconProps: { iconName: "Copy" }, text: "Copy to" },
      {
        key: "delete",
        iconProps: { iconName: "delete" },
        text: "Delete",
        onClick: () => console.log(contextMenu.id),
      },
    ],
  };
  const outputContextMenu = (c) => {
    if (!c) return;
    return (
      <ContextualMenu
        items={menuProps.items}
        target={{
          x: c.x + 5,
          y: c.y + 5,
        }}
      />
    );
  };
  //=====================================================================
  return (
    <Stack
      tokens={{}}
      styles={{
        root: {
          height: window.innerHeight - 36,
          overflowY: "scroll",
          overflowX: "hidden",
        },
      }}
      onClick={() => {
        setCurrent();
        setContextMenu();
      }}
    >
      {outputContextMenu(contextMenu)}
      <Simulate
        showDLG={dlgs.simulateDLG}
        closeDLG={() => setDlgs({ ...dlgs, simulateDLG: !dlgs.simulateDLG })}
      />
      <Stack
        horizontal
        styles={{ root: { height: 37, justifyContent: "center" } }}
      >
        <Stack.Item>
          <FontIcon
            iconName={(function() {
              switch (folder) {
                case 0:
                  return "DocumentSet";
                /*case 1:
                  return "FabricOpenFolderHorizontal";*/
                case 1:
                  return "PeopleRepeat";
                case 2:
                  return "GlobeFavorite";
                case 3:
                  return "RecycleBin";
                default:
                  return "Error";
              }
            })()}
            style={iconClass}
          />
        </Stack.Item>
        <Text variant="large" styles={dateTextStyles}>
          {(function() {
            switch (folder) {
              case 0:
                return "My documents";
              /*case 1:
                return "Recently opened";*/
              case 1:
                return "Shared with me";
              case 2:
                return "Public examples";
              case 3:
                return "Trash";
              default:
                return "Loading!";
            }
          })()}
        </Text>
      </Stack>
      <Stack
        horizontal
        wrap
        tokens={{
          childrenGap: 10,
        }}
        style={{
          root: {
            overflowY: "scroll",
            overflowX: "hidden",
            paddingRight: 10,
          },
        }}
      >
        {files.map((doc) => (
          <Usersingleitem
            key={doc.key}
            doc={doc}
            width={300}
            setContextMenu={setContextMenu}
          />
        ))}
      </Stack>
    </Stack>
  );
}

export default Useritems;
