import React, { useState, useContext, Fragment } from "react";
import { Link } from "react-router-dom";
import AuthContext from "../../context/auth/authContext";
import config from "react-global-configuration";

import {
  PrimaryButton,
  Stack,
  TextField,
  FontSizes,
  FontWeights,
  MessageBar,
  MessageBarType,
  Spinner,
  SpinnerSize,
  Dialog,
  DialogType,
  DialogFooter,
} from "@fluentui/react";

import logo from "../../assets/img/logo.png";

//======================================================================================
const validateEmail = (email) => {
  // eslint-disable-next-line
  var re = /^(([^<>()\[\]\\.,;:\s@"]+(\.[^<>()\[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/;
  return re.test(String(email).toLowerCase());
};
function checkPassword(str) {
  let a = /[A-Z]/.test(str);
  a = a && /[a-z]/.test(str);
  a = a && /[0-9]/.test(str);
  a = a && /[!@#$%^&*()_+\-=\[\]{};':"\\|,.<>\/?]+/.test(str);
  return a;
}
//======================================================================================

const RecoverPassword = (props) => {
  //--------------------------------------------------------------------
  const [conf, setConf] = useState({
    email: "",
    newpassword: "",
    confirmation: "",
    errorMessage: null,
    successfull: false,
    isWaiting: false,
    hideDialog: true,
  });
  const [message, setMessage] = useState("");
  //--------------------------------------------------------------------
  const authContext = useContext(AuthContext);
  const { recoverPassword, confirmRecoveredPassword } = authContext;
  //--------------------------------------------------------------------
  const changePassword = () => {
    if (conf.newpassword.length < 8) {
      setConf({
        ...conf,
        errorMessage: "Your password must be at least 8 characters long.",
        isWaiting: false,
      });
    } else if (!checkPassword(conf.newpassword)) {
      setConf({
        ...conf,
        errorMessage:
          "Your password must have small and captial letters, numbers and symbols.",
        isWaiting: false,
      });
    } else {
      confirmRecoveredPassword(conf.email, conf.confirmation, conf.newpassword)
        .then(() => {
          setConf({
            ...conf,
            isWaiting: false,
            hideDialog: false,
          });
        })
        .catch(() => {
          setConf({
            ...conf,
            errorMessage: "Sorry! It was impossible to change your password.",
            isWaiting: false,
          });
        });
    }
  };
  //--------------------------------------------------------------------
  const onSubmit = (event) => {
    event.preventDefault();
    if (conf.successfull) {
      changePassword();
      return;
    }
    if (!validateEmail(conf.email)) {
      setMessage("Please provide a valid email!");
      return;
    }
    setConf({
      ...conf,
      isWaiting: true,
    });
    recoverPassword(conf.email)
      .then((data) => {
        setConf({
          ...conf,
          errorMessage: null,
          successfull: true,
          isWaiting: false,
        });
      })
      .catch((err) => {
        setConf({
          ...conf,
          errorMessage:
            "Sorry! at this moment you can not change your password.",
          successfull: false,
          isWaiting: false,
        });
      });
  };
  //============================================================================
  const onChange = (e) => {
    setConf({
      ...conf,
      [e.target.name]: e.target.value,
      errorMessage: null,
    });
  };
  //============================================================================
  const errMsg = (
    <MessageBar
      messageBarType={MessageBarType.severeWarning}
      dismissButtonAriaLabel="Close"
      onDismiss={() => setMessage("")}
    >
      {conf.errorMessage}
    </MessageBar>
  );
  //============================================================================

  const toggleHideDialog = () => {
    window.location.href = config.get("homepage") + "/";
  };

  const dialogContentProps = {
    type: DialogType.normal,
    title: "Successful Operation",
    closeButtonAriaLabel: "Close",
    subText: "Your password has successfully changed!",
  };
  const successDialog = (
    <Dialog
      hidden={conf.hideDialog}
      onDismiss={toggleHideDialog}
      closeButtonAriaLabel="Close"
      dialogContentProps={dialogContentProps}
    >
      <DialogFooter>
        <PrimaryButton onClick={toggleHideDialog} text="Login" />
      </DialogFooter>
    </Dialog>
  );
  //============================================================================
  const confirmationEdits = () => {
    return (
      <Fragment>
        <p>
          You have received an email with the confirmation code. Please type
          this code in the box below. Then provide a new password.
        </p>
        <Stack.Item>
          <TextField
            placeholder="Confirmation Code"
            name="confirmation"
            onChange={onChange}
          />
        </Stack.Item>
        <Stack.Item>
          <TextField
            placeholder="New Password"
            name="newpassword"
            type="password"
            onChange={onChange}
          />
        </Stack.Item>
      </Fragment>
    );
  };
  //============================================================================

  const loginBox = (
    <form onSubmit={onSubmit}>
      <Stack
        style={{
          border: "1px solid #c0c0c0",
          borderRadius: "5px",
          padding: "20px",
          marginTop: "10px",
          width: "320px",
        }}
        tokens={{ childrenGap: 10 }}
      >
        {conf.errorMessage === null ? null : errMsg}
        {successDialog}
        <span
          style={{
            fontSize: FontSizes.large,
            fontWeight: FontWeights.bold,
            textAlign: "center",
          }}
        >
          Password Recovery
        </span>
        <p>
          Please enter your email address below. The confirmation code will be
          sent to your email.
        </p>
        <Stack.Item>
          <TextField
            placeholder="Email"
            name="email"
            onChange={onChange}
            disabled={conf.successfull}
          />
        </Stack.Item>
        {conf.successfull ? confirmationEdits() : null}
        <PrimaryButton
          text={conf.successfull ? "Set new password" : "Recover"}
          type="submit"
          allowDisabledFocus
          checked={true}
          disabled={conf.isWaiting}
        />
        {conf.isWaiting ? <Spinner size={SpinnerSize.small} /> : null}
        <Stack.Item align="center">
          <Link to="/login">Return to login page</Link>
        </Stack.Item>
      </Stack>
    </form>
  );
  //============================================================================
  return (
    <Stack
      horizontalAlign="center"
      verticalAlign="stretch"
      style={{ margin: "auto" }}
      grow={true}
      tokens={{ maxWidth: 360, root: { marginTop: "-100px" } }}
    >
      <Stack.Item>
        <div style={{ height: "10px" }} />
      </Stack.Item>
      <Stack.Item>
        <Link to="/">
          <img src={logo} alt="Engipipe Logo" />
        </Link>
      </Stack.Item>
      <Stack.Item>{loginBox}</Stack.Item>
    </Stack>
  );
};

export default RecoverPassword;
