import React, { useEffect, useContext, useState } from "react";
//import { AccountContext } from "../auth/Account";
import AuthContext from "../../context/auth/authContext";
import DesignsContext from "../../context/designs/designsContext";

import NavBar from "./NavBar";
import LeftBar from "./LeftBar";
import Useritems from "./Useritems";
import Detail from "./Detail";

//import DocContext from "../../context/doc/docContext";

import { Stack } from "@fluentui/react";

const WorkSpace = (props) => {
  //============================================================================
  //const { logout, getSession } = useContext(AccountContext);
  const authContext = useContext(AuthContext);
  const { getSession, authenticate } = authContext;
  //============================================================================
  const [user, setUser] = useState({
    accessToken: null,
    idToken: null,
  });
  //============================================================================
  const designsContext = useContext(DesignsContext);
  const { getFiles } = designsContext;
  //============================================================================
  //const docContext = useContext(DocContext);
  //============================================================================

  useEffect(() => {
    getSession()
      .then((session) => {
        console.log("==>", session.accessToken);
        setUser({ accessToken: session.accessToken, idToken: session.idToken });
        //docContext.clearDoc();
        getFiles(0);
      })
      .catch((err) => {
        props.history.push("/login");
      });
    // eslint-disable-next-line
  }, []);
  //---------------------------------------------------------------------------
  const contextMenu = () => {
    return (<Callout
          coverTarget
          ariaLabelledBy={labelId}
          role="dialog"
          className={styles.callout}
          onDismiss={toggleIsCalloutVisible}
          target={`#${buttonId}`}
          isBeakVisible={false}
          setInitialFocus
        >
          <Text block variant="xLarge" className={styles.title} id={labelId}>
            I'm covering the target!
          </Text>
          <div className={styles.actions}>
            <DefaultButton onClick={toggleIsCalloutVisible} text="Click to dismiss" />
          </div>
        </Callout>);
  }
  //---------------------------------------------------------------------------
  //tokens={{ root: { position: "fixed" } }} verticalFill
  return (
    <Stack verticalFill styles={{ root: { width: "100%" } }}>
      <Stack.Item order={1} disableShrink>
        <NavBar />
      </Stack.Item>
      <Stack.Item order={2} grow verticalFill>
        <Stack horizontal>
          <Stack.Item order={1}>
            <LeftBar />
          </Stack.Item>
          <Stack.Item
            order={2}
            styles={{ root: { minWidth: 320, paddingLeft: 5 } }}
            grow
          >
            <Useritems />
          </Stack.Item>
          <Stack.Item
            order={3}
            styles={{
              root: {
                maxWidth: 450,
                minWidth: 450,
                paddingRight: 5,
              },
            }}
            grow
          >
            <Detail />
          </Stack.Item>
        </Stack>
      </Stack.Item>
    </Stack>
  );
};

export default WorkSpace;
