/* eslint-disable */
import React, { useState, useContext, useEffect } from "react";
import PropTypes from "prop-types";
import DesignsContext from "../../context/designs/designsContext";

import {
  Dialog,
  TextField,
  DefaultButton,
  PrimaryButton,
  DialogFooter,
  DialogType,
} from "@fluentui/react";

const CreateDocument = ({ showDLG, closeDLG }) => {
  //============================================================================
  const [go, setGo] = useState(false);
  //============================================================================
  const [file, setFile] = useState({
    title: "",
    description: "",
  });
  const { title, description } = file;
  //============================================================================
  const designsContext = useContext(DesignsContext);
  const { createFile, current, loading } = designsContext;
  //============================================================================
  const modelProps = {
    isBlocking: false,
  };
  //============================================================================
  const dialogContentProps = {
    type: DialogType.largeHeader,
    title: "Create a new document",
    subText: "You need to provide a title for your document.",
  };
  //============================================================================
  useEffect(() => {
    if (go) {
      //console.log("Created", current);
      closeDLG();
    }
    if (loading) setGo(true);
    // eslint-disable-next-line
  }, [loading]);
  //============================================================================
  const onCancel = () => {
    closeDLG();
  };
  //============================================================================
  const onCreate = () => {
    createFile(title, description);
  };
  //============================================================================
  const onChange = (e) => {
    setFile({
      ...file,
      [e.target.name]: e.target.value,
    });
  };

  //============================================================================
  return (
    <Dialog
      hidden={!showDLG}
      onDismiss={onCancel}
      dialogContentProps={dialogContentProps}
      modalProps={modelProps}
      minWidth={600}
    >
      <TextField label="Title" name="title" onChange={onChange} />
      <TextField
        label="Description"
        multiline
        rows={3}
        name="description"
        onChange={onChange}
      />

      <DialogFooter>
        <PrimaryButton onClick={onCreate} text="Create" />
        <DefaultButton onClick={onCancel} text="Cancel" />
      </DialogFooter>
    </Dialog>
  );
};

CreateDocument.propTypes = {
  showDLG: PropTypes.bool.isRequired,
  closeDLG: PropTypes.func.isRequired,
};

export default CreateDocument;
