import { initializeIcons } from "@fluentui/font-icons-mdl2";
import { registerIcons } from "@fluentui/react";
import googleIcon from "./logo_google.js";
import selectIcon from "./iconSelect.js";
import panIcon from "./iconPan.js";
import fitIcon from "./iconFit.js";
import fluidIcon from "./iconFluid.js";
import sourceIcon from "./iconSource.js";
import pipeIcon from "./iconPipe.js";
import demandIcon from "./iconDemand.js";
import pumpIcon from "./iconPump.js";
import fittingsIcon from "./iconFittings.js";
import valveIcon from "./iconValve.js";
import calculationsIcon from "./iconCalculations.js";
import reportIcon from "./iconReport.js";
import helpIcon from "./iconHelp.js";
import hintIcon from "./iconHint.js";
import userIcon from "./iconUser.js";
import menuIcon from "./iconMenu.js";
import { setIconOptions } from "@fluentui/react/lib/Styling";

function RegisterIcons() {
  setIconOptions({
    disableWarnings: true,
  });
  initializeIcons();

  //============================================
  registerIcons({
    icons: {
      "google-svg": googleIcon,
      "select-svg": selectIcon,
      "pan-svg": panIcon,
      "fit-svg": fitIcon,
      "fluid-svg": fluidIcon,
      "source-svg": sourceIcon,
      "pipe-svg": pipeIcon,
      "demand-svg": demandIcon,
      "pump-svg": pumpIcon,
      "fittings-svg": fittingsIcon,
      "valve-svg": valveIcon,
      "calculations-svg": calculationsIcon,
      "report-svg": reportIcon,
      "help-svg": helpIcon,
      "user-svg": userIcon,
      "menu-svg": menuIcon,
      "hint-svg": hintIcon,
    },
  });
  //============================================
}

export default RegisterIcons;
