/* eslint-disable */
import {
  GET_FILES,
  SET_CURRENTFILE,
  SET_FILELOADING,
  GET_SIMULATIONS,
} from "../types";

export default (state, action) => {
  switch (action.type) {
    case GET_FILES:
      return {
        ...state,
        folder: action.payload.folder,
        files: action.payload.files,
        current: null,
        loading: false,
      };
    case GET_SIMULATIONS:
      return {
        ...state,
        simulations: action.payload,
        loading: false,
      };
    case SET_CURRENTFILE:
      return {
        ...state,
        current: action.payload,
        loading: false,
      };
    case SET_FILELOADING:
      return {
        ...state,
        loading: true,
      };

    default:
      return state;
  }
};
