import {
  REGISTER_SUCCESS,
  REGISTER_FAIL,
  LOGIN_SUCCESS,
  LOGIN_FAIL,
  LOGOUT,
  CLEAR_ERRORS,
} from "../types";

const AuthState = (props) => {
  const initialState = {
    accessToken: "",
    idToken: "",
    isAuthenticated: false,
    loading: false,
    error: null,
  };
  //----------------------------------------------------------------------
  const [state, dispatch] = useReducer(authReducer, initialState);
  //----------------------------------------------------------------------
  function parseJwt (token) {
    var base64Url = token.split('.')[1];
    var base64 = base64Url.replace(/-/g, '+').replace(/_/g, '/');
    var jsonPayload = decodeURIComponent(window.atob(base64).split('').map(function(c) {
        return '%' + ('00' + c.charCodeAt(0).toString(16)).slice(-2);
    }).join(''));

    return JSON.parse(jsonPayload);
};  
  //------------------------------------------------------------------------
  const getSession = async () => {
    return await new Promise((resolve, reject) => {
      const user = Pool.getCurrentUser();
      if (user) {
        user.getSession((err, session) => {
          if (err) {
            reject();
          } else {
            let a = parseJwt(session.idToken.jwtToken);
            dispatch({
              type: LOGIN_SUCCESS,
              payload: {
                accessToken: session.accessToken.jwtToken,
                idToken: session.idToken.jwtToken,
                email: initialState.email,
                name:a.name
              },
            });
            resolve(session);
          }
        });
      } else {
        reject();
      }
    });
  };
  //------------------------------------------------------------------------
  const authenticate = async (Username, Password) => {
    return await new Promise((resolve, reject) => {
      const user = new CognitoUser({ Username, Pool });
      const authDetails = new AuthenticationDetails({ Username, Password });

      user.authenticateUser(authDetails, {
        onSuccess: (data) => {
          let sub = "";
          user.getUserAttributes(function (err, result) {
            if (err) {
              alert(err.message || JSON.stringify(err));
              console.log("%%%%%%%>", err.message);
              console.log("%%%%%%%>", JSON.stringify(err));
              return;
            }
            sub = result.filter((r) => r.getName() == "sub")[0].getValue();
          });

          dispatch({
            type: LOGIN_SUCCESS,
            payload: {
              accessToken: data.accessToken,
              idToken: data.idToken,
              email: Username,
              sub: sub,
            },
          });
          resolve(data);
        },
        onFailure: (err) => {
          //console.error("onFailure", err);
          dispatch({ type: LOGIN_FAIL, payload: "E001 :: " + err });
          reject(err);
        },
        newPasswordRequired: (data) => {
          console.log("newPasswordRequired", data);
          dispatch({ type: LOGIN_FAIL, payload: "E002 :: " + data });
          resolve(data);
        },
      });
    });
  };
  //------------------------------------------------------------------------
  const logout = () => {
    const user = Pool.getCurrentUser();
    if (user) {
      user.getSession((err, session) => {
        if (err) {
          //-- problem with siging
          dispatch({ type: LOGOUT });
          user.signOut();
        } else {
          //-- do somethinf
          return new Promise((resolve, reject) => {
            user.globalSignOut({
              onSuccess: () => {
                dispatch({ type: LOGOUT });
                resolve();
              },
              onFailure: reject,
            });
          });
        }
      });
    }
  };
  //------------------------------------------------------------------------
  const recoverPassword = async (Username) => {
    const user = new CognitoUser({ Username, Pool });
    return await new Promise((resolve, reject) => {
      user.forgotPassword({
        onSuccess: (data) => {
          resolve(data);
        },
        onFailure: (err) => {
          reject(err);
        },
      });
    });
  };
  //------------------------------------------------------------------------
  const confirmRecoveredPassword = async (Username, verification, password) => {
    const user = new CognitoUser({ Username, Pool });
    return await new Promise((resolve, reject) => {
      user.confirmPassword(verification, password, {
        onSuccess: (data) => {
          resolve(data);
        },
        onFailure: (err) => {
          reject(err);
        },
      });
    });
  };
  //------------------------------------------------------------------------

  //-- Load user ---------------------------------------------------------
  /*const loadUser = () => {
    if (localStorage.token) {
      setAuthToken(localStorage.token);
      dispatch({
        type: LOGIN_SUCCESS,
        payload: {
          token: localStorage.token,
          email: localStorage.email,
          username: localStorage.username,
        },
      });
    } else {
      setAuthToken();
    }
  };
  //----------------------------------------------------------------------

  //-- Registe user ------------------------------------------------------
  const register = async (formData) => {
    const config = {
      headers: {
        "Content-Type": "application/json",
      },
    };

    try {
      const res = await axios.post("/api/users", formData, config);
      dispatch({ type: REGISTER_SUCCESS, payload: res.data });
      loadUser();
    } catch (err) {
      dispatch({ type: REGISTER_FAIL, payload: err.response.data.msg });
    }
  };
  //----------------------------------------------------------------------

  //-- login user --------------------------------------------------------
  const login = async (email, password) => {
    try {
      const res = await axios.post(`${config.get("backendURL")}/auth/local`, {
        identifier: email,
        password: password,
      });
      dispatch({
        type: LOGIN_SUCCESS,
        payload: {
          token: res.data.jwt,
          username: res.data.user.username,
          email: res.data.user.email,
        },
      });
      loadUser();
    } catch (err) {
      dispatch({ type: LOGIN_FAIL, payload: "E001" });
    }
  };
  //----------------------------------------------------------------------

  //-- Google or other providers set context
  const setLogin = (token, username, email) => {
    dispatch({ type: LOGIN_SUCCESS, payload: { token, username, email } });
  };
  //----------------------------------------------------------------------

  //-- logout
  const logout = async () => {
    const tk = localStorage.token;
    setAuthToken();
    try {
      await axios.post(`${config.get("backendURL")}/auth/token/revoke`, {
        token: tk,
      });
      dispatch({ type: LOGOUT });
    } catch (err) {
      //console.log(localStorage.token, err);
      dispatch({ type: LOGOUT });
    }
  };

  //-- clear erros
  const clearErrors = () => {
    dispatch({ type: CLEAR_ERRORS });
  };*/

  return (
    <AuthContext.Provider
      value={{
        token: state.token,
        isAuthenticated: state.isAuthenticated,
        loading: state.loading,
        username: state.username,
        name:state.name,
        email: state.email,
        error: state.error,        
        authenticate,
        getSession,
        logout,
        recoverPassword,
        confirmRecoveredPassword,
      }}
    >
      {props.children}
    </AuthContext.Provider>
  );
};

export default AuthState;
