import React, { useState } from "react";
import PropTypes from "prop-types";
//import DesignsContext from "../../context/designs/designsContext";

import {
  Dialog,
  TextField,
  DefaultButton,
  PrimaryButton,
  DialogFooter,
  DialogType,
  CompoundButton,
  Stack,
  StackItem,
  SpinButton,
  Checkbox,
} from "@fluentui/react";

const Simulate = ({ showDLG, closeDLG }) => {
  //============================================================================
  //const [go, setGo] = useState(false);
  //============================================================================
  const [solverType, setSolverType] = useState("CPU");
  const [serviceType, setServiceType] = useState("Cloud");
  const [restart, setRestart] = useState(false);
  const [replaceSim, setReplaceSim] = useState(false);
  const [inforMe, setInforMe] = useState(false);
  const [title, setTitle] = useState();
  //============================================================================
  /*const designsContext = useContext(DesignsContext);
  const { getSimulations } = designsContext;*/
  //============================================================================
  const modelProps = {
    isBlocking: false,
  };
  //============================================================================
  const dialogContentProps = {
    type: DialogType.largeHeader,
    title: "Start a new simulation",
    subText: "Please specify the configuration for executing your simulation.",
  };
  //============================================================================
  const onCancel = () => {
    closeDLG();
  };
  //============================================================================
  const onCreate = () => {
    console.log("Ask for simulation API");
    closeDLG();
  };
  //============================================================================
  const onChange = (e) => {
    setTitle(e.target.value);
  };
  //============================================================================
  const buttonStyle = {
    root: {
      width: 170,
      borderColor: "#767676",
    },
    rootPressed: {
      backgroundColor: "#0f9960",
      borderColor: "#767676",
    },
    rootHovered: {
      backgroundColor: "#0f9960",
      borderColor: "#767676",
      color: "#fff",
    },
  };
  //============================================================================
  return (
    <Dialog
      hidden={!showDLG}
      onDismiss={onCancel}
      dialogContentProps={dialogContentProps}
      modalProps={modelProps}
      minWidth={550}
    >
      <Stack vertical tokens={{ childrenGap: 5 }}>
        <Stack
          horizontal
          tokens={{ childrenGap: 8 }}
          styles={{ root: { backgroundColor: "#ebe8e8" } }}
        >
          <StackItem>
            <CompoundButton
              secondaryText={
                serviceType === "Cloud"
                  ? "Run simulation on cloud."
                  : "Run simulation locally."
              }
              primary={solverType === "CPU"}
              onClick={() => setSolverType("CPU")}
              styles={{ root: { width: 165 } }}
            >
              CPU
            </CompoundButton>
            <SpinButton
              label="No. of CPUs"
              defaultValue="1"
              min={1}
              max={10}
              styles={{ root: { width: 150 } }}
              disabled={solverType === "GPU"}
            />
          </StackItem>
          <StackItem>
            <CompoundButton
              secondaryText={
                serviceType === "Cloud"
                  ? "Run simulation on cloud."
                  : "Run simulation locally."
              }
              primary={solverType === "GPU"}
              onClick={() => setSolverType("GPU")}
              styles={{ root: { width: 165 } }}
            >
              GPU
            </CompoundButton>
            <SpinButton
              label="No. of GPUs"
              defaultValue="1"
              min={1}
              max={1}
              styles={{
                root: { width: 160 },
              }}
              disabled={solverType === "CPU"}
            />
          </StackItem>
          <Stack
            vertical
            tokens={{ childrenGap: 5 }}
            styles={{ root: { paddingLeft: 20 } }}
          >
            <CompoundButton
              text="Run on cloud"
              primary={serviceType === "Cloud"}
              onClick={() => setServiceType("Cloud")}
              styles={{
                ...buttonStyle,
                root: {
                  backgroundColor: serviceType === "Cloud" ? "#0f9960" : null,
                },
              }}
            />
            <CompoundButton
              buttonType="hero"
              text="Run locally"
              secondaryText="(on your computer)"
              primary={serviceType === "Local"}
              onClick={() => setServiceType("Local")}
              styles={{
                ...buttonStyle,
                root: {
                  backgroundColor: serviceType === "Local" ? "#0f9960" : null,
                },
              }}
            />
          </Stack>
        </Stack>
        <Stack horizontal styles={{ root: { paddingTop: 10 } }}>
          <Checkbox
            label="Use other simulations as the initial data"
            styles={{
              root: { paddingLeft: 5, paddingTop: 5, marginRight: 16 },
            }}
            onChange={() => setRestart(!restart)}
          />
          <DefaultButton text="Select Initial/Restart" disabled={!restart} />
        </Stack>
        <Stack horizontal>
          <Checkbox
            label="Replace the last simulation"
            styles={{
              root: { paddingLeft: 5, paddingTop: 5, marginRight: 16 },
            }}
            onChange={() => setReplaceSim(!replaceSim)}
          />
          <TextField
            name="title"
            placeholder="Provide a title for the simulation..."
            onChange={onChange}
            styles={{ fieldGroup: { width: 280 } }}
            disabled={replaceSim}
            autoComplete="off"
            value={replaceSim ? null : title}
          />
        </Stack>
        <Checkbox
          label="Inform me when the simulation is done"
          styles={{
            root: { paddingLeft: 5, paddingTop: 5, marginRight: 16 },
          }}
          onChange={() => setInforMe(!inforMe)}
        />
      </Stack>
      <DialogFooter>
        <PrimaryButton onClick={onCreate} text="Simulate" />
        <DefaultButton onClick={onCancel} text="Cancel" />
      </DialogFooter>
    </Dialog>
  );
};

Simulate.propTypes = {
  showDLG: PropTypes.bool.isRequired,
  closeDLG: PropTypes.func.isRequired,
};

export default Simulate;
